<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="notifySearch">筛选</a-button>
              <a-button type="primary" @click="notifyAdd">新增</a-button>
              <a-button type="primary" :disabled="!notifyHasSelected" @click="notifyDel">删除</a-button>
              <span style="margin-left: 8px">
								<template v-if="notifyHasSelected">
									已选中 {{ NotifySelectedRowKeys.length }} 行
								</template>
							</span>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button @click="notifyExportDate">导出</a-button>
              <a-button :disabled="!notifyHasSelected" @click="notifyPrintDate">打印</a-button>
            </a-space>
          </a-col>
        </a-row>
        <div v-if="NotifySearchShow">
          <a-row :gutter="6" class="notifySearchRow">
					<a-col span="4">
						<a-input v-model="NotifySearchDate.Title"  addon-before="标题" />
					</a-col>
					<a-col span="4">
						<a-input v-model="NotifySearchDate.Content"  addon-before="内容" />
					</a-col>
          </a-row>
          <a-row class="notifySearchBtn" style="text-align: center;">
            <a-space size="large">
              <a-button type="primary" @click="notifyEmptySearch">重置</a-button>
              <a-button type="primary" @click="notifyGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table
          :row-selection="{ selectedRowKeys: NotifySelectedRowKeys, onChange: notifySelectChange }"
          :columns="NotifyColumns"
          :data-source="NotifyData"
          rowKey="ID"
          :pagination="NotifyPaginationOption"
          bordered
          size="small"
      >
        <span slot="notifyDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="notifyRecordUpdate(record)">修改</a-button>
          </a-space>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import xlsx from 'xlsx'
import moment from 'moment'

const NotifyColumns = [
	{
		title: 'ID',
		dataIndex: 'ID',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.ID - b.ID,
		scopedSlots: {customRender: 'ID'},
	},
	{
		title: '标题',
		dataIndex: 'Title',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.Title - b.Title,
		scopedSlots: {customRender: 'Title'},
	},
	{
		title: '内容',
		dataIndex: 'Content',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.Content - b.Content,
		scopedSlots: {customRender: 'Content'},
	},
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'notifyDoSome'},
  },
];
export default {
  name: "NotifyAdmin",
  data() {
    return {
      NotifyData: [],
      NotifyColumns,
      NotifySelectedRowKeys: [],
      NotifySearchDate: {},
      NotifySearchShow: false,
      NotifyPaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 10,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.NotifyPaginationOption.defaultCurrent = current
          this.NotifyPaginationOption.defaultPageSize = pageSize
          this.notifyGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.NotifyPaginationOption.defaultCurrent = current
          this.NotifyPaginationOption.defaultPageSize = pageSize
          this.notifyGets()
        },
      },
    };
  },
  computed: {
    notifyHasSelected() {
      return this.NotifySelectedRowKeys.length > 0;
    },
  },
  created() {
    this.notifyGets()
  },
  methods: {
    notifyRecordUpdate(record) {
      this.$router.push({name: 'NotifyDetail', params: {formTitle: 'Update Notify', formData: record}})
    },
    notifySearch() {
      this.NotifySearchShow = !this.NotifySearchShow
      this.NotifyPaginationOption.defaultCurrent = 1
    },
    notifyEmptySearch() {
      this.NotifySearchDate = {}
      this.NotifyPaginationOption.defaultCurrent = 1
    },
    async notifyGets() {
      const {data: res} = await this.$http.post('notifys', this.NotifySearchDate, {
        params: {
          pageNum: this.NotifyPaginationOption.defaultCurrent,
          pageSize: this.NotifyPaginationOption.defaultPageSize,
        }
      })
      this.NotifyPaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.NotifyData = res.data.notifys
      this.NotifyPaginationOption.total = res.data.total
      this.NotifySearchShow = false
    },
    notifyAdd() {
      this.$router.push({name: 'NotifyDetail', params: {formTitle: 'Add Notify'}}).catch((err) => err)
    },
    notifyDel() {
      if (this.NotifySelectedRowKeys.length > 1) return this.$message.warn('不允许删除多行')
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.notifyDelOk()
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async notifyDelOk() {
      const {data: res} = await this.$http.delete('notify/' + this.NotifySelectedRowKeys)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.NotifySelectedRowKeys = []
      this.notifyGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    notifySelectChange(selectedRowKeys) {
      console.log('NotifySelectedRowKeys changed: ', selectedRowKeys);
      this.NotifySelectedRowKeys = selectedRowKeys;
    },
    async notifyExportDate() {
      const {data: res} = await this.$http.post('notifys/exp', this.NotifySearchDate)
      if (res.code !== 200) return this.$message.warn(res.msg)
      let tableData = res.data.notifys
      let ws = xlsx.utils.aoa_to_sheet(tableData)
      let wb = xlsx.utils.book_new()
      xlsx.utils.book_append_sheet(wb, ws, 'notify') // 工作簿名称
      xlsx.writeFile(wb, 'notify.xlsx') // 保存的文件名
    },
    notifyPrintDate() {
      console.log('print data')
      this.NotifySelectedRowKeys.forEach((item, index) => {
        console.log(item, index)
      })
      this.NotifySelectedRowKeys = []
    },
  },
  filters: {
    filterTimeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    }
  }
};
</script>

<style>

.notifySearchRow > .ant-col, .notifySearchBtn {
  margin-top: 10px;
}

.ant-table-column-title {
  font-weight: bold;
}

</style>
