<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-space size="small">
            <a-button type="primary" @click="menuTreeAddLevel1">新增一级菜单</a-button>
          </a-space>
        </a-row>
      </div>
      <a-table
          :columns="MenuTreeColumns"
          :data-source="MenuTreeData"
          rowKey="ID"
          :pagination="MenuTreePaginationOption"
          :expandedRowKeys.sync="MenuTreeExpandedRowKeys"
          bordered
          size="small"
      >
        <span slot="Level" slot-scope="Level">
          <a-tag v-if="Level===1" color="red">{{ Level|levelFullName }}</a-tag>
          <a-tag v-if="Level===2" color="cyan">{{ Level|levelFullName }}</a-tag>
          <a-tag v-if="Level===3" color="orange">{{ Level|levelFullName }}</a-tag>
          <a-tag v-if="Level > 3">{{ Level|levelFullName }}</a-tag>
        </span>
        <span slot="menuTreeDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="menuTreeAdd(record)">新增</a-button>
            <a-button type="primary" size="small" @click="menuTreeDel(record)">删除</a-button>
            <a-button type="primary" size="small" @click="menuTreeUpdate(record)">修改</a-button>
          </a-space>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>

const MenuTreeColumns = [
  {
    title: '菜单层级',
    dataIndex: 'Level',
    align: 'left',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Level - b.Level,
    width: 300,
    scopedSlots: {customRender: 'Level'},
  },
  {
    title: '菜单名称',
    dataIndex: 'Name',
    align: 'left',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Name - b.Name,
    scopedSlots: {customRender: 'Name'},
  },
  {
    title: 'ID',
    dataIndex: 'ID',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.ID - b.ID,
    width: 100,
    scopedSlots: {customRender: 'ID'},
  },
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'menuTreeDoSome'},
  },
];
export default {
  name: "MenuTree",
  data() {
    return {
      MenuTreeData: [],
      MenuTreeColumns,
      MenuTreePaginationOption: {
        defaultPageSize: 100,
      },
      MenuTreeExpandedRowKeys: [],
    };
  },
  computed: {},
  created() {
    this.menuGets()
  },
  methods: {
    menuTreeAddLevel1() {
      this.$router.push({name: 'MenuDetail', params: {formTitle: 'Add L1 Menu'}})
    },
    menuTreeAdd(record) {
      this.$router.push({name: 'MenuDetail', params: {formTitle: 'Add Menu', formData: record}})
    },
    menuTreeDel(record) {
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.menuTreeDelOk(record.ID)
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async menuTreeDelOk(id) {
      const {data: res} = await this.$http.delete('menu/' + id)
      if (res.code !== 200) return this.$message.warn(`删除失败，${res.msg}`)
      this.menuGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    menuTreeUpdate(record) {
      this.$router.push({name: 'MenuDetail', params: {formTitle: 'Update Menu', formData: record}})
    },
    async menuGets() {
      const {data: res} = await this.$http.get('menu/tree')
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.MenuTreeData = res.data.menuTree
      this.initMenuTreeExpandedRowKeys()
    },
    initMenuTreeExpandedRowKeys() {
      if (this.MenuTreeData) {
        this.MenuTreeData.forEach((item) => {
          this.MenuTreeExpandedRowKeys.push(item.ID)
        })
      }
    },
  },
  filters: {
    filterTimeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    },
    levelFullName(val) {
      return `第 ${val} 级`
    },
  }
};
</script>

<style>

.menuSearchRow > .ant-col, .menuSearchBtn {
  margin-top: 10px;
}

.ant-table-column-title {
  font-weight: bold;
}

#input-switch {
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa
}

</style>
