<template>
  <a-layout-header class="headerBtn">
    <a-dropdown>
      <a-avatar v-text="username" class="userInfo ant-dropdown-link" size="default"/>
      <a-menu slot="overlay">
        <a-menu-item @click="changePassword">
          <a>修改密码</a>
        </a-menu-item>
        <a-menu-item @click="logout">
          <a>退出登录</a>
        </a-menu-item>
      </a-menu>
    </a-dropdown>
    <!--footer=""，隐藏底部默认按钮-->
    <a-modal v-model="show" title="修改密码" footer="">
      <ChangePassword :showModal="showModal"></ChangePassword>
    </a-modal>
  </a-layout-header>
</template>

<script>
import ChangePassword from "./ChangePassword";

export default {
  name: "Header",
  components: {ChangePassword},
  data() {
    return {
      username: 'Eddie',
      show: false,
    }
  },
  methods: {
    changePassword() {
      this.show = true
    },
    logout() {
      window.localStorage.clear('zys-token')
      window.sessionStorage.clear('zys-token')
      this.$router.push({name: 'Login'})
    },
    getUsername() {
      this.username = window.localStorage.getItem('username')
    },
    showModal(show) {
      this.show = show
    }
  },
  created() {
    this.getUsername()
  }
}
</script>

<style scoped>
.headerBtn {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.ant-avatar {
  padding: 0;
}
</style>