<template>
  <div>
    <a-card :title="UserFormTitle">
      <a-form-model ref="UserFormRef" :rules="UserFormRules" :model="UserFormData">
        <a-form-model-item hasFeedback prop="Username" label="用户">
          <a-input :disabled="UserItemDisabled" v-model="UserFormData.Username"/>
        </a-form-model-item>
        <!--				<a-form-model-item hasFeedback prop="Password" label="密码">-->
        <!--					<a-input :disabled="UserItemDisabled" v-model="UserFormData.Password" />-->
        <!--				</a-form-model-item>-->
        <a-form-model-item hasFeedback prop="Phone" label="手机">
          <a-input v-model="UserFormData.Phone"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Email" label="邮箱">
          <a-input v-model="UserFormData.Email"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="IsAdmin" label="管理员">
          <a-switch v-model="UserFormData.IsAdmin"/>
        </a-form-model-item>
        <a-form-model-item label="头像">
          <a-upload name="file" list-type="picture"
                    v-model="UserFormData.Card"
                    :file-list="UserFormData.Card"
                    :remove="userCardRemove"
                    :custom-request="(data) => userCardUpload(data)">
            <a-button>
              <a-icon type="upload"/>
              点击上传
            </a-button>
          </a-upload>
        </a-form-model-item>
        <a-form-model-item label="部门">
          <a-select
              mode="multiple"
              v-model="UserDepartmentList"
              style="width: 100%"
              placeholder="选择部门"
              :filter-option="userSelectFilterOption"
              @change="userDepartmentChange"
          >
            <a-select-option v-for="item in DepartmentList" :key="item.ID">
              {{ item.Name }}
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="角色">
          <a-select
              mode="multiple"
              v-model="UserRoleList"
              style="width: 100%"
              placeholder="选择角色"
              :filter-option="userSelectFilterOption"
              @change="userRoleChange"
          >
            <a-select-option v-for="item in RoleList" :key="item.ID">
              {{ item.Name }}
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item class="userFormBtn">
          <a-space>
            <a-button type="info" @click="userFormReset">取消</a-button>
            <a-button type="primary" @click="userFormOk">确定</a-button>
          </a-space>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
import {Url} from "../../plugin/http";
import {isNumberID} from "../../util/isNumberID";

export default {
  name: "UserDetail",
  data() {
    return {
      UserFormTitle: '',
      UserFormData: {},
      UserFormRules: {
        Username: [
          {required: true, message: '用户不能为空', trigger: 'blur'},
        ],
        Password: [
          {required: true, message: '密码不能为空', trigger: 'blur'},
        ],
        Phone: [
          {required: true, message: '手机不能为空', trigger: 'blur'},
        ],
        Email: [
          {required: true, message: '邮箱不能为空', trigger: 'blur'},
        ],
        // IsAdmin: [
        // {required: true, message: '管理员不能为空', trigger: 'blur'},
        // ],
      },
      UserItemDisabled: false,
      UserCardUploadUrl: Url + "/file/upload",
      RoleList: undefined,
      DepartmentList: undefined,
      UserRoleList: [],
      UserDepartmentList: [],
    }
  },
  created() {
    this.loadRoleList().catch((err) => err)
    this.loadDepartmentList().catch((err) => err)
    this.createFunc()
  },
  methods: {
    async createFunc() {
      this.UserFormTitle = this.$route.params.formTitle
      if (this.UserFormTitle === 'Add User') {
        this.UserFormData.Card = []
        console.log('add user.')
      } else if (this.UserFormTitle === 'Update User') {
        this.UserFormData = this.$route.params.formData
        this.UserItemDisabled = true
        const {data: res} = await this.$http.get('user/' + this.UserFormData.ID)
        if (res.code !== 200) return this.$message.warn(res.msg)
        this.UserFormData = res.data.user
        this.UserRoleList = []
        if (this.UserFormData.Role) {
          this.UserFormData.Role.forEach((item) => {
            this.UserRoleList.push(item.ID)
          })
        }
        // if (this.UserFormData.Department) {
        //   this.UserFormData.Department.forEach((item) => {
        //     this.UserDepartmentList.push(item.ID)
        //   })
        // }
        this.UserFormData.Department ? this.UserFormData.Department.forEach((item) => {
          this.UserDepartmentList.push(item.ID)
        }) : undefined
      } else {
        this.$router.push({name: 'UserAdmin'}).catch((err) => err)
      }
    },
    userFormReset() {
      this.$refs.UserFormRef.resetFields();
      this.$router.push({name: 'UserAdmin'}).catch((err) => err)
    },
    userFormOk() {
      if (this.UserFormTitle === 'Add User') {
        this.userAdd()
      } else if (this.UserFormTitle === 'Update User') {
        this.userUpdate()
      } else {
        this.$message.error('提交错误')
        this.$router.push({name: 'UserAdmin'}).catch((err) => err)
      }
    },
    async userAdd() {
      this.UserFormData.Password = this.UserFormData.Phone
      const {data: res} = await this.$http.post('user', this.UserFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('新增成功')
      this.$router.push({name: 'UserAdmin'}).catch((err) => err)
    },
    async userUpdate() {
      const {data: res} = await this.$http.put('user/' + this.UserFormData.ID, this.UserFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'UserAdmin'}).catch((err) => err)
    },
    userCardRemove(file) {
      const index = this.UserFormData.Card.indexOf(file);
      const newFileList = this.UserFormData.Card.slice();
      newFileList.splice(index, 1);
      this.UserFormData.Card = newFileList;
    },
    async userCardUpload({file}) { // 文件上传
      // let uuid = UUID()
      const formData = new FormData()
      formData.append('file', file)
      // formData.append('uuid', uuid) //向后端传递其他参数，比如UID
      const {data: res} = await this.$http.post(this.UserCardUploadUrl, formData)
      if (res.code !== 200) {
        console.log(res)
        return this.$message.warn(res.msg)
      }
      // this.modalFormData.Card = [] //上传前清空，同仅保留最新
      this.UserFormData.Card.push({
        "name": file.name,
        "uid": file.uid,
        "url": res.data.url,
        "save_name": res.data.uid,
      })
    },
    async loadRoleList() {
      const {data: res} = await this.$http.post("roles")
      this.RoleList = res.data.roles
    },
    async loadDepartmentList() {
      const {data: res} = await this.$http.post('departments')
      this.DepartmentList = res.data.departments
    },
    userRoleChange() {
      this.UserFormData.Role = []
      this.UserRoleList.forEach((item) => {
        // if (isNumberID(item)) {
        //   this.UserFormData.Role.push({"id": item})
        // }
        isNumberID(item) ? this.UserFormData.Role.push({"id": item}) : undefined
      })
    },
    userDepartmentChange() {
      this.UserFormData.Department = []
      this.UserDepartmentList.forEach((item) => {
        // if (isNumberID(item)) {
        //   this.UserFormData.Department.push({"id": item})
        // }
        isNumberID(item) ? this.UserFormData.Department.push({"id": item}) : undefined
      })
    },
    userSelectFilterOption(input, option) {
      return option.componentOptions.children[0].text.toLowerCase().indexOf(input.toLowerCase()) >= 0
    },
  },
}
</script>

<style scoped>

.userFormBtn {
  display: flex;
  justify-content: flex-end;
}

</style>
