<template>
  <div>
    <a-card :title="MenuFormTitle">
      <a-form-model ref="MenuFormRef" :rules="MenuFormRules" :model="MenuFormData">
        <a-form-model-item hasFeedback prop="PID" label="PID">
          <a-input-number :disabled="MenuItemDisabled" v-model.number="MenuFormData.PID"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Level" label="层级">
          <a-input-number :disabled="MenuItemDisabled" v-model.number="MenuFormData.Level"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Name" label="名称">
          <a-input v-model="MenuFormData.Name"/>
        </a-form-model-item>
        <a-form-model-item class="menuFormBtn">
          <a-space>
            <a-button type="info" @click="menuFormReset">取消</a-button>
            <a-button type="primary" @click="menuFormOk">确定</a-button>
          </a-space>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
export default {
  name: "MenuDetail",
  data() {
    return {
      MenuFormTitle: '',
      MenuFormData: {},
      MenuFormRules: {
        PID: [
          {required: true, message: 'PID不能为空', trigger: 'blur'},
        ],
        Level: [
          {required: true, message: '层级不能为空', trigger: 'blur'},
        ],
        Name: [
          {required: true, message: '名称不能为空', trigger: 'blur'},
        ],
      },
      MenuItemDisabled: false,
    }
  },
  created() {
    this.MenuFormTitle = this.$route.params.formTitle
    if (this.MenuFormTitle === 'Add Menu') {
      this.MenuFormData.PID = this.$route.params.formData.ID
      this.MenuFormData.Level = this.$route.params.formData.Level + 1
      this.MenuItemDisabled = true
    } else if (this.MenuFormTitle === 'Add L1 Menu') {
      this.MenuFormData.PID = 0
      this.MenuFormData.Level = 1
      this.MenuItemDisabled = true
    } else if (this.MenuFormTitle === 'Update Menu') {
      this.MenuFormData = this.$route.params.formData
      this.MenuItemDisabled = true
    } else {
      this.$router.push({name: 'MenuAdmin'}).catch((err) => err)
    }
  },
  methods: {
    menuFormReset() {
      this.$refs.MenuFormRef.resetFields();
      this.$router.push({name: 'MenuAdmin'}).catch((err) => err)
    },
    menuFormOk() {
      if (this.MenuFormTitle === 'Add Menu') {
        this.menuAdd()
      } else if (this.MenuFormTitle === 'Add L1 Menu') {
        this.menuAdd()
      } else if (this.MenuFormTitle === 'Update Menu') {
        this.menuUpdate()
      } else {
        this.$message.error('提交错误')
        this.$router.push({name: 'MenuAdmin'}).catch((err) => err)
      }
    },
    async menuAdd() {
      const {data: res} = await this.$http.post('menu', this.MenuFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('新增成功')
      this.$router.push({name: 'MenuAdmin'}).catch((err) => err)
    },
    async menuUpdate() {
      const {data: res} = await this.$http.put('menu/' + this.MenuFormData.ID, this.MenuFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'MenuAdmin'}).catch((err) => err)
    },
  },
}
</script>

<style scoped>

.menuFormBtn {
  display: flex;
  justify-content: flex-end;
}

</style>
