<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-space size="small">
            <a-button type="primary" @click="departmentTreeAddLevel1">新增一级部门</a-button>
          </a-space>
        </a-row>
      </div>
      <a-table
          :columns="DepartmentTreeColumns"
          :data-source="DepartmentTreeData"
          rowKey="ID"
          :pagination="DepartmentTreePaginationOption"
          :expandedRowKeys.sync="DepartmentTreeExpandedRowKeys"
          bordered
          size="small"
      >
        <span slot="Level" slot-scope="Level">
          <a-tag v-if="Level===1" color="red">{{ Level|levelFullName }}</a-tag>
          <a-tag v-if="Level===2" color="cyan">{{ Level|levelFullName }}</a-tag>
          <a-tag v-if="Level===3" color="orange">{{ Level|levelFullName }}</a-tag>
          <a-tag v-if="Level > 3">{{ Level|levelFullName }}</a-tag>
        </span>
        <span slot="departmentTreeDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="departmentTreeSortUp(record)">上移</a-button>
            <a-button type="primary" size="small" @click="departmentTreeSortDown(record)">下移</a-button>
            <a-button type="primary" size="small" @click="departmentTreeAdd(record)">新增</a-button>
            <a-button type="primary" size="small" @click="departmentTreeDel(record)">删除</a-button>
            <a-button type="primary" size="small" @click="departmentTreeUpdate(record)">修改</a-button>
          </a-space>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>

const DepartmentTreeColumns = [
  {
    title: '部门层级',
    dataIndex: 'Level',
    align: 'left',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Level - b.Level,
    width: 300,
    scopedSlots: {customRender: 'Level'},
  },
  {
    title: '部门编号',
    dataIndex: 'Code',
    align: 'left',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Code - b.Code,
    width: 200,
    scopedSlots: {customRender: 'Code'},
  },
  {
    title: '部门名称',
    dataIndex: 'Name',
    align: 'left',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Name - b.Name,
    scopedSlots: {customRender: 'Name'},
  },
  // {
  //   title: 'ID',
  //   dataIndex: 'ID',
  //   align: 'center',
  //   sortDirections: ['descend', 'ascend'],
  //   sorter: (a, b) => a.ID - b.ID,
  //   width: 100,
  //   scopedSlots: {customRender: 'ID'},
  // },
  // {
  //   title: '排序',
  //   dataIndex: 'Sort',
  //   align: 'center',
  //   sortDirections: ['descend', 'ascend'],
  //   sorter: (a, b) => a.Sort - b.Sort,
  //   width: 100,
  //   scopedSlots: {customRender: 'Sort'},
  // },
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'departmentTreeDoSome'},
  },
];
export default {
  name: "DepartmentTree",
  data() {
    return {
      DepartmentTreeData: [],
      DepartmentTreeColumns,
      DepartmentTreePaginationOption: {
        defaultPageSize: 100,
      },
      DepartmentTreeExpandedRowKeys: [],
    };
  },
  computed: {},
  created() {
    this.departmentGets()
  },
  methods: {
    departmentTreeAddLevel1() {
      this.$router.push({name: 'DepartmentDetail', params: {formTitle: 'Add L1 Department'}})
    },
    departmentTreeAdd(record) {
      this.$router.push({name: 'DepartmentDetail', params: {formTitle: 'Add Department', formData: record}})
    },
    departmentTreeDel(record) {
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.departmentTreeDelOk(record.ID)
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async departmentTreeDelOk(id) {
      const {data: res} = await this.$http.delete('department/' + id)
      if (res.code !== 200) return this.$message.warn(`删除失败，${res.msg}`)
      this.departmentGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    departmentTreeUpdate(record) {
      this.$router.push({name: 'DepartmentDetail', params: {formTitle: 'Update Department', formData: record}})
    },
    async departmentGets() {
      const {data: res} = await this.$http.get('department/tree')
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.DepartmentTreeData = res.data.departmentTree
      this.initDepartmentTreeExpandedRowKeys()
    },
    initDepartmentTreeExpandedRowKeys() {
      if (this.DepartmentTreeData) {
        this.DepartmentTreeData.forEach((item) => {
          this.DepartmentTreeExpandedRowKeys.push(item.ID)
        })
      }
    },
    departmentTreeSortUp(record) {
      record.Sort -= 1
      this.departmentSortUpdate(record)
    },
    departmentTreeSortDown(record) {
      record.Sort += 1
      this.departmentSortUpdate(record)
    },
    async departmentSortUpdate(record) {
      const {data: res} = await this.$http.put(`department/${record.ID}/${record.Sort}`)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('排序修改完成')
      this.departmentGets().catch((err)=>err)
    },
  },
  filters: {
    filterTimeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    },
    levelFullName(val) {
      return `第 ${val} 级`
    },
  }
};
</script>

<style>

.departmentSearchRow > .ant-col, .departmentSearchBtn {
  margin-top: 10px;
}

.ant-table-column-title {
  font-weight: bold;
}

#input-switch {
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa
}

</style>
