<template>
  <div>
    <a-card title="#最新通知#">
      <a-list item-layout="vertical" :data-source="NotifyData">
        <a-list-item slot="renderItem" :key="notifyItem.ID" slot-scope="notifyItem, notifyIndex">
          <a-list-item-meta :description="notifyItem.UpdatedAt|filterTimeFormat">
            <a slot="title" @click="gotoNotifyDetail(notifyItem,notifyIndex)">{{ notifyItem.Title }}</a>
          </a-list-item-meta>
          {{ notifyItem.Content }}
        </a-list-item>
      </a-list>
    </a-card>
  </div>
</template>

<script>
import moment from "moment";

export default {
  name: "Index",
  data() {
    return {
      NotifyData: [],
    }
  },
  created() {
    this.notifyGets()
  },
  methods: {
    async notifyGets() {
      const {data: res} = await this.$http.post('notifys')
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.NotifyData = res.data.notifys
    },
    gotoNotifyDetail(item, index) {
      console.log(item)
      console.log(index)
    }
  },
  filters: {
    filterTimeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    },
  },
}
</script>

<style scoped>

.ant-list-item-meta-title a{
  font-weight: bold;
}

</style>