<template>
    <a-card class="serverStatus">
        <b style="font-size: 18px">刷新速率</b> <i v-text="'默认5秒，滑动调节。当前间隔：'+timeLevel+'秒'"></i>
        <a-slider @afterChange="onAfterChange" v-model="timeLevel" :min="1" :max="10"/>
        <a-table
                rowKey="HostIp"
                :columns="columns"
                :data-source="ServerList"
                bordered
        >
            <span slot="Cpu" slot-scope="Cpu">
                <a-tag
                        :color="Cpu >= 90 ? '#f50' : Cpu >= 70 ? 'orange' : 'green'"
                >
                    {{Cpu}}%
                </a-tag>
            </span>
            <span slot="Mem" slot-scope="Mem">
                <a-tag
                        :color="Mem >= 90 ? '#f50' : Mem >= 70 ? 'orange' : 'green'"
                >
                    {{Mem}}%
                </a-tag>
            </span>
            <span slot="Disk" slot-scope="Disk">
                <a-tag
                        :color="Disk >= 90 ? '#f50' : Disk >= 70 ? 'orange' : 'green'"
                >
                    {{Disk}}%
                </a-tag>
            </span>

        </a-table>
    </a-card>
</template>

<script>
    const columns = [
        {
            title: 'IP地址',
            dataIndex: 'HostIp',
            align: 'center',
            width: '160px',
            sortDirections: ['descend', 'ascend'],
            // sorter: (a, b) => Number(a.HostIp.replaceAll('.','')) - Number(b.HostIp.replaceAll('.','')),
            sorter: (a, b) => a.HostIp.replace(/[^\d]/g,"") - b.HostIp.replace(/[^\d]/g,""),
        },
        {
            title: '处理器',
            dataIndex: 'Cpu',
            align: 'center',
            scopedSlots: {customRender: 'Cpu'},
            width: '110px',
            sortDirections: ['descend', 'ascend'],
            sorter: (a, b) => a.Cpu - b.Cpu,
        },
        {
            title: '内存',
            dataIndex: 'Mem',
            align: 'center',
            scopedSlots: {customRender: 'Mem'},
            width: '110px',
            sortDirections: ['descend', 'ascend'],
            sorter: (a, b) => a.Mem - b.Mem,
        },
        {
            title: '硬盘',
            dataIndex: 'Disk',
            align: 'center',
            scopedSlots: {customRender: 'Disk'},
            width: '110px',
            defaultSortOrder: 'descend',
            sorter: (a, b) => a.Disk - b.Disk,
        },
        {
            title: '主机名',
            dataIndex: 'HostName',
            align: 'center',
            width: '200px',
        },
        {
            title: '系统版本',
            dataIndex: 'Platform',
            align: 'center',
        },
        {
            title: '系统启动时间',
            dataIndex: 'BootTime',
            align: 'center',
            width: '160px',
            sortDirections: ['descend', 'ascend'],
            sorter: (a, b) => a.BootTime.replace(/[^\d]/g,"") - b.BootTime.replace(/[^\d]/g,""),
        },
        {
            title: '最后心跳时间',
            dataIndex: 'PushTime',
            align: 'center',
            width: '160px',
            sortDirections: ['descend', 'ascend'],
            sorter: (a, b) => a.PushTime.replace(/[^\d]/g,"") - b.PushTime.replace(/[^\d]/g,""),
        },
    ]

    export default {
        name: "ServerStatus",
        data() {
            return {
                columns,
                ServerList: [],
                timer: '',
                timeLevel: 5,
            }
        },
        methods: {
            async getServerStatus() {
                const {data: res} = await this.$http.get('client_infos')
                if (res.code !== 200) return this.$message.warn('状态获取失败')
                this.ServerList = res.data.serverList
            },
            onAfterChange() {
                this.$message.success(`间隔${this.timeLevel}秒`)
                clearInterval(this.timer)
                this.timer = setInterval(this.getServerStatus, 1000 * this.timeLevel)
            }
        },
        created() {
            this.getServerStatus()
            this.timer = setInterval(this.getServerStatus, 1000 * this.timeLevel)
        },
        beforeDestroy() {
            clearInterval(this.timer)
        }
    }
</script>

