<template>
  <div>
    <a-card :title="EnumFormTitle">
      <a-form-model ref="EnumFormRef" :rules="EnumFormRules" :model="EnumFormData">
        <a-form-model-item hasFeedback prop="PID" label="PID">
          <a-input-number disabled v-model.number="EnumFormData.PID"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Name" label="名称">
          <a-input v-model="EnumFormData.Name"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Val" label="枚举值">
          <a-input-number v-model.number="EnumFormData.Val"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Sort" label="排序">
          <a-input-number v-model.number="EnumFormData.Sort"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="IsEnable" label="启用">
          <a-switch v-model="EnumFormData.IsEnable"/>
        </a-form-model-item>
        <a-form-model-item class="enumFormBtn">
          <a-space>
            <a-button type="info" @click="enumFormReset">取消</a-button>
            <a-button type="primary" @click="enumFormOk">确定</a-button>
          </a-space>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
export default {
  name: "EnumDetail",
  data() {
    return {
      EnumFormTitle: '',
      EnumFormData: {},
      EnumFormRules: {
        PID: [
          {required: true, message: 'PID不能为空', trigger: 'blur'},
        ],
        Name: [
          {required: true, message: '名称不能为空', trigger: 'blur'},
        ],
        Val: [
          {required: true, message: '枚举值不能为空', trigger: 'blur'},
        ],
        Sort: [
          {required: true, message: '排序不能为空', trigger: 'blur'},
        ],
      },
      EnumItemDisabled: false,
    }
  },
  created() {
    this.EnumFormTitle = this.$route.params.formTitle
    if (this.EnumFormTitle === 'Add Enum') {
      this.EnumFormData.IsEnable = true
      this.EnumFormData.PID = this.$route.params.pid
    } else if (this.EnumFormTitle === 'Update Enum') {
      this.EnumFormData = this.$route.params.formData
      this.EnumItemDisabled = true
    } else {
      this.$router.push({name: 'EnumAdmin'}).catch((err) => err)
    }
  },
  methods: {
    enumFormReset() {
      this.$refs.EnumFormRef.resetFields();
      this.$router.push({name: 'EnumAdmin'}).catch((err) => err)
    },
    enumFormOk() {
      if (this.EnumFormTitle === 'Add Enum') {
        this.enumAdd()
      } else if (this.EnumFormTitle === 'Update Enum') {
        this.enumUpdate()
      } else {
        this.$message.error('提交错误')
        this.$router.push({name: 'EnumAdmin'}).catch((err) => err)
      }
    },
    async enumAdd() {
      const {data: res} = await this.$http.post('enum', this.EnumFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('新增成功')
      this.$router.push({name: 'EnumAdmin'}).catch((err) => err)
    },
    async enumUpdate() {
      const {data: res} = await this.$http.put('enum/' + this.EnumFormData.ID, this.EnumFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'EnumAdmin'}).catch((err) => err)
    },
  },
}
</script>

<style scoped>

.enumFormBtn {
  display: flex;
  justify-content: flex-end;
}

</style>
