<template>
  <div>
    <a-card :title="ConnectInfoFormTitle">
      <a-form-model ref="ConnectInfoFormRef" :rules="ConnectInfoFormRules" :model="ConnectInfoFormData">
        <a-form-model-item hasFeedback prop="Type" label="类型">
          <a-select v-model="ConnectInfoFormData.Type" @change="typeChange()">
            <a-select-option v-for="(item,index) in DbTypeOption"
                             :key="index"
                             :value="item.name"
            >
              {{ item.name }}
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Ip" label="地址">
          <a-input v-model="ConnectInfoFormData.Ip"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Port" label="端口">
          <a-input v-model="ConnectInfoFormData.Port"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Name" label="名称">
          <a-input v-model="ConnectInfoFormData.Name"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="User" label="用户">
          <a-input v-model="ConnectInfoFormData.User"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Password" label="密码">
          <a-input v-model="ConnectInfoFormData.Password"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Schemas" label="实例">
          <a-input v-model="ConnectInfoFormData.Schemas"/>
        </a-form-model-item>
        <a-form-model-item class="connectInfoFormBtn">
          <a-space>
            <a-button type="info" @click="connectInfoFormReset">取消</a-button>
            <a-button type="primary" @click="connectInfoFormOk">确定</a-button>
          </a-space>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
export default {
  name: "ConnectInfoDetail",
  data() {
    return {
      DbTypeOption: [
        {name: 'oracle', port: '1521'},
        {name: 'mysql', port: '3306'},
        {name: 'sqlserver', port: '1433'},
      ],
      ConnectInfoFormTitle: '',
      ConnectInfoFormData: {Port: ''}, // 变量预声明，解决变量被关联带出后无法再修改的问题
      ConnectInfoFormRules: {
        Ip: [
          {required: true, message: '地址不能为空', trigger: 'blur'},
        ],
        Port: [
          {required: true, message: '端口不能为空', trigger: 'blur'},
        ],
        Type: [
          {required: true, message: '类型不能为空', trigger: 'blur'},
        ],
        Name: [
          {required: true, message: '名称不能为空', trigger: 'blur'},
        ],
        User: [
          {required: true, message: '用户不能为空', trigger: 'blur'},
        ],
        Password: [
          {required: true, message: '密码不能为空', trigger: 'blur'},
        ],
        Schemas: [
          {required: true, message: '实例不能为空', trigger: 'blur'},
        ],
      },
      ConnectInfoItemDisabled: false,
    }
  },
  created() {
    this.ConnectInfoFormTitle = this.$route.params.formTitle
    if (this.ConnectInfoFormTitle === '新增_数据库连接') {
      console.log('add')
    } else if (this.ConnectInfoFormTitle === '修改_数据库连接') {
      this.ConnectInfoFormData = this.$route.params.formData
      this.ConnectInfoItemDisabled = true
    } else {
      this.$router.push({name: 'ConnectInfoAdmin'}).catch((err) => err)
    }
    // this.ConnectInfoFormData.Port= undefined
  },
  methods: {
    typeChange() {
      this.DbTypeOption.forEach((item) => {
        if (item.name === this.ConnectInfoFormData.Type) {
          return this.ConnectInfoFormData.Port = item.port
        }
      })
    },
    connectInfoFormReset() {
      this.$refs.ConnectInfoFormRef.resetFields();
      this.$router.push({name: 'ConnectInfoAdmin'}).catch((err) => err)
    },
    connectInfoFormOk() {
      if (this.ConnectInfoFormTitle === '新增_数据库连接') {
        this.connectInfoAdd()
      } else if (this.ConnectInfoFormTitle === '修改_数据库连接') {
        this.connectInfoUpdate()
      } else {
        this.$message.error('提交错误')
        this.$router.push({name: 'ConnectInfoAdmin'}).catch((err) => err)
      }
    },
    async connectInfoAdd() {
      const {data: res} = await this.$http.post('connect_info', this.ConnectInfoFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('新增成功')
      this.$router.push({name: 'ConnectInfoAdmin'}).catch((err) => err)
    },
    async connectInfoUpdate() {
      const {data: res} = await this.$http.put('connect_info/' + this.ConnectInfoFormData.ID, this.ConnectInfoFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'ConnectInfoAdmin'}).catch((err) => err)
    },
  },
}
</script>

<style scoped>

.connectInfoFormBtn {
  display: flex;
  justify-content: flex-end;
}

.ant-input-disabled, .ant-input-number-disabled {
  color: #000000A6 !important;
  background-color: #fafafa !important;
}

</style>
