<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="userSearch">筛选</a-button>
              <a-button type="primary" @click="userAdd">新增</a-button>
              <a-button type="primary" :disabled="!userHasSelected" @click="userDel">删除</a-button>
              <span style="margin-left: 8px">
								<template v-if="userHasSelected">
									已选中 {{ UserSelectedRowKeys.length }} 行
								</template>
							</span>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button @click="userExportDate">导出</a-button>
              <a-button :disabled="!userHasSelected" @click="userPrintDate">打印</a-button>
            </a-space>
          </a-col>
        </a-row>
        <div v-if="UserSearchShow">
          <a-row :gutter="6" class="userSearchRow">
            <a-col span="4">
              <a-input v-model="UserSearchDate.Username" addon-before="用户"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="UserSearchDate.Phone" addon-before="手机"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="UserSearchDate.Email" addon-before="邮箱"/>
            </a-col>
            <a-col span="4">
              <a-input id="input-switch" disabled defaultValue="管理员">
                <a-switch v-model="UserSearchDate.IsAdmin" slot="addonAfter"/>
              </a-input>
            </a-col>
          </a-row>
          <a-row class="userSearchBtn" style="text-align: center;">
            <a-space size="large">
              <a-button type="primary" @click="userEmptySearch">重置</a-button>
              <a-button type="primary" @click="userGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table
          :row-selection="{ selectedRowKeys: UserSelectedRowKeys, onChange: userSelectChange }"
          :columns="UserColumns"
          :data-source="UserData"
          rowKey="ID"
          :pagination="UserPaginationOption"
          bordered
          size="small"
      >
                <span slot="IsAdmin" slot-scope="IsAdmin">
                    <a-icon v-if="IsAdmin" type="check" style="color: green"/>
                    <a-icon v-if="!IsAdmin" type="close" style="color: red"/>
                </span>
        <span slot="userDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="userRecordUpdate(record)">修改</a-button>
            <a-button type="default" size="small" @click="resetPassword(record)">重置密码</a-button>
          </a-space>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import xlsx from 'xlsx'
import moment from 'moment'

const UserColumns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.ID - b.ID,
    scopedSlots: {customRender: 'ID'},
  },
  {
    title: '用户',
    dataIndex: 'Username',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Username - b.Username,
    scopedSlots: {customRender: 'Username'},
  },
  {
    title: '手机',
    dataIndex: 'Phone',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Phone - b.Phone,
    scopedSlots: {customRender: 'Phone'},
  },
  {
    title: '邮箱',
    dataIndex: 'Email',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Email - b.Email,
    scopedSlots: {customRender: 'Email'},
  },
  {
    title: '管理员',
    dataIndex: 'IsAdmin',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.IsAdmin - b.IsAdmin,
    scopedSlots: {customRender: 'IsAdmin'},
  },
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'userDoSome'},
  },
];
export default {
  name: "UserAdmin",
  data() {
    return {
      UserData: [],
      UserColumns,
      UserSelectedRowKeys: [],
      UserSearchDate: {},
      UserSearchShow: false,
      UserPaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 10,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.UserPaginationOption.defaultCurrent = current
          this.UserPaginationOption.defaultPageSize = pageSize
          this.userGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.UserPaginationOption.defaultCurrent = current
          this.UserPaginationOption.defaultPageSize = pageSize
          this.userGets()
        },
      },
    };
  },
  computed: {
    userHasSelected() {
      return this.UserSelectedRowKeys.length > 0;
    },
  },
  created() {
    this.userGets()
  },
  methods: {
    userAdd() {
      this.$router.push({name: 'UserDetail', params: {formTitle: 'Add User'}}).catch((err) => err)
    },
    userDel() {
      if (this.UserSelectedRowKeys.length > 1) return this.$message.warn('不允许删除多行')
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.userDelOk()
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async userDelOk() {
      const {data: res} = await this.$http.delete('user/' + this.UserSelectedRowKeys)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.UserSelectedRowKeys = []
      this.userGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    userRecordUpdate(record) {
      this.$router.push({name: 'UserDetail', params: {formTitle: 'Update User', formData: record}})
    },
    userSearch() {
      this.UserSearchShow = !this.UserSearchShow
      this.UserPaginationOption.defaultCurrent = 1
    },
    userEmptySearch() {
      this.UserSearchDate = {}
      this.UserPaginationOption.defaultCurrent = 1
    },
    async userGets() {
      const {data: res} = await this.$http.post('users', this.UserSearchDate, {
        params: {
          pageNum: this.UserPaginationOption.defaultCurrent,
          pageSize: this.UserPaginationOption.defaultPageSize,
        }
      })
      this.UserPaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.UserData = res.data.users
      this.UserPaginationOption.total = res.data.total
      this.UserSearchShow = false
    },
    userSelectChange(selectedRowKeys) {
      console.log('UserSelectedRowKeys changed: ', selectedRowKeys);
      this.UserSelectedRowKeys = selectedRowKeys;
    },
    async userExportDate() {
      const {data: res} = await this.$http.post('users/exp', this.UserSearchDate)
      if (res.code !== 200) return this.$message.warn(res.msg)
      let tableData = res.data.users
      let ws = xlsx.utils.aoa_to_sheet(tableData)
      let wb = xlsx.utils.book_new()
      xlsx.utils.book_append_sheet(wb, ws, 'user') // 工作簿名称
      xlsx.writeFile(wb, 'user.xlsx') // 保存的文件名
    },
    userPrintDate() {
      console.log('print data')
      this.UserSelectedRowKeys.forEach((item, index) => {
        console.log(item, index)
      })
      this.UserSelectedRowKeys = []
    },
    async resetPassword(record) {
      const {data} = await this.$http.put('user/password/admin', record)
      console.log(data)
      if (data.code !== 200) return this.$message.warn(data.msg)
      this.$message.success(`${record.Username}密码已重置为${record.Phone}`)
    },
  },
  filters: {
    filterTimeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    }
  }
};
</script>

<style>

.userSearchRow > .ant-col, .userSearchBtn {
  margin-top: 10px;
}

.ant-table-column-title {
  font-weight: bold;
}

#input-switch {
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa
}

</style>
