<template>
  <div v-show="lastStock1Idx>=0" style="width: 50%; float: left">
    <a-card id="stock-listen1-card">
      <a-table
          :columns="PriceColumns"
          :data-source="stock1"
          rowKey="ID"
          bordered
          size="small"
      >
        <template slot="title" slot-scope="stockPageData" v-if="lastStock1Idx>=0">
          <i>
            {{ stockPageData[lastStock1Idx].name }} {{
              stockPageData[lastStock1Idx].type
            }}{{ stockPageData[lastStock1Idx].symbol }}
            <span
                :style="{color:(isQuiet ? stockPageData[lastStock1Idx].updown >= 0 ? 'orangered' : 'green' : '')}">
              {{
                stockPageData[lastStock1Idx].Stock.Hand * 100 * stockPageData[lastStock1Idx].updown | qianweifuFloat
              }}
            </span>
          </i>
          <span style="padding-left: 1em">
          <span
              v-if="stockPageData[lastStock1Idx].Stock.UpCall > 0 && stockPageData[lastStock1Idx].price > stockPageData[lastStock1Idx].Stock.UpCall"
              style="color: orangered;border: orangered 1px dotted">
            出手
          </span>
          <span
              v-if="stockPageData[lastStock1Idx].Stock.DownCall > 0 && stockPageData[lastStock1Idx].price < stockPageData[lastStock1Idx].Stock.DownCall"
              style="color: orangered;border: orangered 1px dotted">
            入手
          </span>
          </span>
        </template>
        <span slot="Percent" slot-scope="Percent">
          <span :style="{color:(isQuiet ? Percent >= 0 ? 'orangered' : 'green' : '')}">{{ Percent|percent }}</span>
        </span>
        <span slot="Volume" slot-scope="Volume">{{ Volume|qianweifu }}</span>
        <span slot="BidAsk0" slot-scope="BidAsk0">{{ BidAsk0|qianweifu }}</span>
        <span slot="Bid0" slot-scope="Bid0">{{ Bid0|qianweifu }}</span>
        <span style="text-align: end" slot="Ask0" slot-scope="Ask0">{{ Ask0|qianweifu }}</span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import {convertQianweifu} from "../../util/convertQianweifu";
import {convertQianweifuWithFloat} from "../../util/convertQianweifuWithFloat";

const PriceColumns = [
  {
    title: '昨收',
    dataIndex: 'yestclose',
    align: 'center',
    width: 70,
  },
  {
    title: '今开',
    dataIndex: 'open',
    align: 'center',
    width: 70,
  },
  {
    title: '最新价',
    dataIndex: 'price',
    align: 'center',
    width: 70,
  },
  {
    title: '涨幅',
    dataIndex: 'percent',
    align: 'center',
    scopedSlots: {customRender: 'Percent'},
    width: 70,
  },
  {
    title: '涨价',
    dataIndex: 'updown',
    align: 'center',
    width: 70,
  },
  {
    title: '成交量',
    dataIndex: 'volume',
    align: 'center',
    scopedSlots: {customRender: 'Volume'},
  },
  {
    title: '买卖差',
    dataIndex: 'bidask0',
    align: 'center',
    scopedSlots: {customRender: 'BidAsk0'},
    width: 100,
  },
  {
    title: '买总数',
    dataIndex: 'bid0',
    align: 'center',
    scopedSlots: {customRender: 'Bid0'},
    width: 100,
  },
  {
    title: '卖总数',
    dataIndex: 'ask0',
    align: 'center',
    scopedSlots: {customRender: 'Ask0'},
    width: 100,
  },
  {
    title: '最高',
    dataIndex: 'high',
    align: 'center',
    width: 70,
  },
  {
    title: '最低',
    dataIndex: 'low',
    align: 'center',
    width: 70,
  },
];
export default {
  name: "PriceListen1",
  data() {
    return {
      PriceData: [],
      PriceColumns,
    };
  },
  props: {
    stock1: [],
    isQuiet: true,
  },
  computed: {
    lastStock1Idx() {
      return this.stock1.length - 1
    }
  },
  created() {
  },
  methods: {},
  filters: {
    timeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    },
    percent(val) {
      return Number(val * 100).toFixed(2) + "%"
    },
    qianweifu(val) {
      // return val
      return convertQianweifu(val);
    },
    qianweifuFloat(val) {
      return convertQianweifuWithFloat(val);
    },
  }
};
</script>

