<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="menuSearch">筛选</a-button>
              <a-button type="primary" @click="menuAdd">新增</a-button>
              <a-button type="primary" :disabled="!menuHasSelected" @click="menuDel">删除</a-button>
              <span style="margin-left: 8px">
								<template v-if="menuHasSelected">
									已选中 {{ MenuSelectedRowKeys.length }} 行
								</template>
							</span>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button @click="menuExportDate">导出</a-button>
              <a-button :disabled="!menuHasSelected" @click="menuPrintDate">打印</a-button>
            </a-space>
          </a-col>
        </a-row>
        <div v-if="MenuSearchShow">
          <a-row :gutter="6" class="menuSearchRow">
					<a-col span="4">
						<a-input v-model.number="MenuSearchDate.PID" addon-before="PID" />
					</a-col>
					<a-col span="4">
						<a-input v-model.number="MenuSearchDate.Level" addon-before="层级" />
					</a-col>
					<a-col span="4">
						<a-input v-model="MenuSearchDate.Name"  addon-before="名称" />
					</a-col>
          </a-row>
          <a-row class="menuSearchBtn" style="text-align: center;">
            <a-space size="large">
              <a-button type="primary" @click="menuEmptySearch">重置</a-button>
              <a-button type="primary" @click="menuGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table
          :row-selection="{ selectedRowKeys: MenuSelectedRowKeys, onChange: menuSelectChange }"
          :columns="MenuColumns"
          :data-source="MenuData"
          rowKey="ID"
          :pagination="MenuPaginationOption"
          bordered
          size="small"
      >
        <span slot="menuDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="menuRecordUpdate(record)">修改</a-button>
          </a-space>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import xlsx from 'xlsx'
import moment from 'moment'

const MenuColumns = [
	{
		title: 'ID',
		dataIndex: 'ID',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.ID - b.ID,
		scopedSlots: {customRender: 'ID'},
	},
	{
		title: 'PID',
		dataIndex: 'PID',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.PID - b.PID,
		scopedSlots: {customRender: 'PID'},
	},
	{
		title: '层级',
		dataIndex: 'Level',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.Level - b.Level,
		scopedSlots: {customRender: 'Level'},
	},
	{
		title: '名称',
		dataIndex: 'Name',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.Name - b.Name,
		scopedSlots: {customRender: 'Name'},
	},
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'menuDoSome'},
  },
];
export default {
  name: "MenuAdmin",
  data() {
    return {
      MenuData: [],
      MenuColumns,
      MenuSelectedRowKeys: [],
      MenuSearchDate: {},
      MenuSearchShow: false,
      MenuPaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 10,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.MenuPaginationOption.defaultCurrent = current
          this.MenuPaginationOption.defaultPageSize = pageSize
          this.menuGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.MenuPaginationOption.defaultCurrent = current
          this.MenuPaginationOption.defaultPageSize = pageSize
          this.menuGets()
        },
      },
    };
  },
  computed: {
    menuHasSelected() {
      return this.MenuSelectedRowKeys.length > 0;
    },
  },
  created() {
    this.menuGets()
  },
  methods: {
    menuAdd() {
      this.$router.push({name: 'MenuDetail', params: {formTitle: 'Add Menu'}}).catch((err) => err)
    },
    menuDel() {
      if (this.MenuSelectedRowKeys.length > 1) return this.$message.warn('不允许删除多行')
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.menuDelOk()
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async menuDelOk() {
      const {data: res} = await this.$http.delete('menu/' + this.MenuSelectedRowKeys)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.MenuSelectedRowKeys = []
      this.menuGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    menuRecordUpdate(record) {
      this.$router.push({name: 'MenuDetail', params: {formTitle: 'Update Menu', formData: record}})
    },
    menuSearch() {
      this.MenuSearchShow = !this.MenuSearchShow
      this.MenuPaginationOption.defaultCurrent = 1
    },
    menuEmptySearch() {
      this.MenuSearchDate = {}
      this.MenuPaginationOption.defaultCurrent = 1
    },
    async menuGets() {
      const {data: res} = await this.$http.post('menus', this.MenuSearchDate, {
        params: {
          pageNum: this.MenuPaginationOption.defaultCurrent,
          pageSize: this.MenuPaginationOption.defaultPageSize,
        }
      })
      this.MenuPaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.MenuData = res.data.menus
      this.MenuPaginationOption.total = res.data.total
      this.MenuSearchShow = false
    },
    menuSelectChange(selectedRowKeys) {
      console.log('MenuSelectedRowKeys changed: ', selectedRowKeys);
      this.MenuSelectedRowKeys = selectedRowKeys;
    },
    async menuExportDate() {
      const {data: res} = await this.$http.post('menus/exp', this.MenuSearchDate)
      if (res.code !== 200) return this.$message.warn(res.msg)
      let tableData = res.data.menus
      let ws = xlsx.utils.aoa_to_sheet(tableData)
      let wb = xlsx.utils.book_new()
      xlsx.utils.book_append_sheet(wb, ws, 'menu') // 工作簿名称
      xlsx.writeFile(wb, 'menu.xlsx') // 保存的文件名
    },
    menuPrintDate() {
      console.log('print data')
      this.MenuSelectedRowKeys.forEach((item, index) => {
        console.log(item, index)
      })
      this.MenuSelectedRowKeys = []
    },
  },
  filters: {
    filterTimeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    }
  }
};
</script>

<style>

.menuSearchRow > .ant-col, .menuSearchBtn {
  margin-top: 10px;
}

.ant-table-column-title {
  font-weight: bold;
}

#input-switch{
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa
}

</style>
