<template>
  <div>
    <a-card>
      {{ VersionHistoryTitle }}的历史版本：
      <div v-if=false>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="versionSearch">筛选</a-button>
              <a-button type="primary" @click="versionAdd">新增</a-button>
              <a-button type="primary" :disabled="!versionHasSelected" @click="versionDel">删除</a-button>
              <span style="margin-left: 8px">
								<template v-if="versionHasSelected">
									已选中 {{ VersionSelectedRowKeys.length }} 行
								</template>
							</span>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button @click="versionExportDate">导出</a-button>
              <a-button :disabled="!versionHasSelected" @click="versionPrintDate">打印</a-button>
            </a-space>
          </a-col>
        </a-row>
        <div v-if="VersionSearchShow">
          <a-row :gutter="[12,8]" class="versionSearchInput">
            <a-col span="4">
              <a-input v-model="VersionSearchDate.Code" addon-before="版本号"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="VersionSearchDate.UpdateLink" addon-before="更新链接"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="VersionSearchDate.Software" addon-before="Software"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="VersionSearchDate.SoftwareId" addon-before="软件ID"/>
            </a-col>
            <a-col span="4">
              <a-input v-model.number="VersionSearchDate.UserId" addon-before="用户ID"/>
            </a-col>
          </a-row>
          <a-row class="versionSearchBtn">
            <a-space size="large">
              <a-button type="primary" @click="versionEmptySearch">重置</a-button>
              <a-button type="primary" @click="versionGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table
          :columns="VersionColumns"
          :data-source="VersionData"
          rowKey="ID"
          :pagination="VersionPaginationOption"
          bordered
          size="small"
      >
        <!--        <span slot="versionDoSome" slot-scope="record">-->
        <!--          <a-space size="small">-->
        <!--            <a-button type="primary" size="small" @click="versionRecordUpdate(record)">修改</a-button>-->
        <!--          </a-space>-->
        <!--        </span>-->
        <span slot="CreatedAt" slot-scope="CreatedAt">
          {{ CreatedAt|timeFormat }}
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import xlsx from 'xlsx'
import moment from 'moment'

const VersionColumns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.ID - b.ID,
    scopedSlots: {customRender: 'ID'},
  },
  {
    title: '版本号',
    dataIndex: 'Code',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Code - b.Code,
    scopedSlots: {customRender: 'Code'},
  },
  {
    title: '更新链接',
    dataIndex: 'UpdateLink',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.UpdateLink - b.UpdateLink,
    scopedSlots: {customRender: 'UpdateLink'},
  },
  {
    title: 'MD5校验值',
    dataIndex: 'Md5Hash',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Md5Hash - b.Md5Hash,
    scopedSlots: {customRender: 'Md5Hash'},
  },
  {
    title: '发布日期',
    dataIndex: 'CreatedAt',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.CreatedAt - b.CreatedAt,
    scopedSlots: {customRender: 'CreatedAt'},
  },
  {
    title: '软件',
    dataIndex: 'Software.Name',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.SoftwareId - b.SoftwareId,
    scopedSlots: {customRender: 'SoftwareId'},
  },
  {
    title: '用户ID',
    dataIndex: 'UserId',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.UserId - b.UserId,
    scopedSlots: {customRender: 'UserId'},
  },
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'versionDoSome'},
  },
];
export default {
  name: "VersionAdmin",
  data() {
    return {
      VersionHistoryTitle: '',
      VersionData: [],
      VersionColumns,
      VersionSelectedRowKeys: [],
      VersionSearchDate: {},
      VersionSearchShow: false,
      VersionPaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 10,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.VersionPaginationOption.defaultCurrent = current
          this.VersionPaginationOption.defaultPageSize = pageSize
          this.versionGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.VersionPaginationOption.defaultCurrent = current
          this.VersionPaginationOption.defaultPageSize = pageSize
          this.versionGets()
        },
      },
    };
  },
  computed: {
    versionHasSelected() {
      return this.VersionSelectedRowKeys.length > 0;
    },
  },
  created() {
    this.VersionSearchDate.SoftwareId = this.$route.params.Software.ID
    this.VersionHistoryTitle = this.$route.params.Software.Name
    this.versionGets()
  },
  methods: {
    versionAdd() {
      this.$router.push({name: 'VersionDetail', params: {formTitle: '新增_版本'}}).catch((err) => err)
    },
    versionDel() {
      if (this.VersionSelectedRowKeys.length > 1) return this.$message.warn('不允许删除多行')
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.versionDelOk()
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async versionDelOk() {
      const {data: res} = await this.$http.delete('version/' + this.VersionSelectedRowKeys)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.VersionSelectedRowKeys = []
      this.versionGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    versionRecordUpdate(record) {
      this.$router.push({name: 'VersionDetail', params: {formTitle: '修改_版本', formData: record}})
    },
    versionSearch() {
      this.VersionSearchShow = !this.VersionSearchShow
      this.VersionPaginationOption.defaultCurrent = 1
    },
    versionEmptySearch() {
      this.VersionSearchDate = {}
      this.VersionPaginationOption.defaultCurrent = 1
    },
    async versionGets() {
      const {data: res} = await this.$http.post('version/list', this.VersionSearchDate, {
        params: {
          pageNum: this.VersionPaginationOption.defaultCurrent,
          pageSize: this.VersionPaginationOption.defaultPageSize,
        }
      })
      this.VersionPaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.VersionData = res.data.versionList
      this.VersionPaginationOption.total = res.data.total
      this.VersionSearchShow = false
    },
    versionSelectChange(selectedRowKeys) {
      console.log('VersionSelectedRowKeys changed: ', selectedRowKeys);
      this.VersionSelectedRowKeys = selectedRowKeys;
    },
    async versionExportDate() {
      const {data: res} = await this.$http.post('version/list/exp', this.VersionSearchDate)
      if (res.code !== 200) return this.$message.warn(res.msg)
      let tableData = res.data.versionList
      let ws = xlsx.utils.aoa_to_sheet(tableData)
      let wb = xlsx.utils.book_new()
      xlsx.utils.book_append_sheet(wb, ws, 'version') // 工作簿名称
      xlsx.writeFile(wb, 'version.xlsx') // 保存的文件名
    },
    versionPrintDate() {
      console.log('print data')
      this.VersionSelectedRowKeys.forEach((item, index) => {
        console.log(item, index)
      })
      this.VersionSelectedRowKeys = []
    },
  },
  filters: {
    timeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    }
  }
};
</script>

<style>

.versionSearchBtn {
  text-align: center;
}

.versionSearchInput, .versionSearchBtn {
  margin-top: 10px !important;
}

.ant-table-column-title {
  font-weight: bold;
}

.ant-input-disabled, .ant-input-number-disabled {
  color: #000000A6 !important;
  background-color: #fafafa !important;
}

</style>
