<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="apiSearch">筛选</a-button>
              <a-button type="primary" @click="apiAdd">新增</a-button>
              <a-button type="primary" :disabled="!apiHasSelected" @click="apiDel">删除</a-button>
              <a-button type="primary" @click="apiInit" :loading="ApiInitLoading">从路由表初始化</a-button>
              <span style="margin-left: 8px">
								<template v-if="apiHasSelected">
									已选中 {{ ApiSelectedRowKeys.length }} 行
								</template>
							</span>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button @click="apiExportDate">导出</a-button>
              <a-button :disabled="!apiHasSelected" @click="apiPrintDate">打印</a-button>
            </a-space>
          </a-col>
        </a-row>
        <!--        <div v-if="ApiSearchShow">-->
        <div v-if="true">
          <a-row :gutter="6" class="apiSearchRow">
            <!--            <a-col span="4">-->
            <!--              <a-input v-model.number="ApiSearchDate.ID" addon-before="ID"/>-->
            <!--            </a-col>-->
            <a-col span="4">
              <a-input v-model="ApiSearchDate.Path" addon-before="路径"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="ApiSearchDate.Method" addon-before="方法"/>
            </a-col>
          </a-row>
          <a-row class="apiSearchBtn" style="text-align: center;">
            <a-space size="large">
              <a-button type="primary" @click="apiEmptySearch">重置</a-button>
              <a-button type="primary" @click="apiGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table
          :row-selection="{ selectedRowKeys: ApiSelectedRowKeys, onChange: apiSelectChange }"
          :columns="ApiColumns"
          :data-source="ApiData"
          rowKey="ID"
          :pagination="ApiPaginationOption"
          bordered
          size="small"
      >
        <span slot="apiDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="apiRecordUpdate(record)">绑定菜单</a-button>
          </a-space>
        </span>
        <span slot="Method" slot-scope="Method">
            <a-tag :color="Method|apiTagColor">{{ Method|apiTagLabel }}</a-tag>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import xlsx from 'xlsx'
import moment from 'moment'

const ApiColumns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.ID - b.ID,
    scopedSlots: {customRender: 'ID'},
  },
  {
    title: '路径',
    dataIndex: 'Path',
    align: 'left',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Path - b.Path,
    scopedSlots: {customRender: 'Path'},
  },
  {
    title: '方法',
    dataIndex: 'Method',
    align: 'left',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Method - b.Method,
    scopedSlots: {customRender: 'Method'},
  },
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'apiDoSome'},
  },
];
const apiMethodOptions = [
  {
    value: 'POST',
    label: '创建',
    color: 'green'
  },
  {
    value: 'GET',
    label: '查看',
    color: 'blue'
  },
  {
    value: 'PUT',
    label: '更新',
    color: 'cyan'
  },
  {
    value: 'DELETE',
    label: '删除',
    color: 'red'
  }
];

export default {
  name: "ApiAdmin",
  data() {
    return {
      ApiData: [],
      ApiColumns,
      ApiSelectedRowKeys: [],
      ApiSearchDate: {},
      ApiSearchShow: false,
      ApiPaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 10,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.ApiPaginationOption.defaultCurrent = current
          this.ApiPaginationOption.defaultPageSize = pageSize
          this.apiGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.ApiPaginationOption.defaultCurrent = current
          this.ApiPaginationOption.defaultPageSize = pageSize
          this.apiGets()
        },
      },
      ApiInitLoading: false,
    };
  },
  computed: {
    apiHasSelected() {
      return this.ApiSelectedRowKeys.length > 0;
    },
  },
  created() {
    this.apiGets()
  },
  methods: {
    apiAdd() {
      this.$router.push({name: 'ApiDetail', params: {formTitle: 'Add Api'}}).catch((err) => err)
    },
    apiDel() {
      if (this.ApiSelectedRowKeys.length > 1) return this.$message.warn('不允许删除多行')
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.apiDelOk()
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async apiDelOk() {
      const {data: res} = await this.$http.delete('api/' + this.ApiSelectedRowKeys)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.ApiSelectedRowKeys = []
      this.apiGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    apiRecordUpdate(record) {
      this.$router.push({name: 'ApiDetail', params: {formTitle: 'Update Api', formData: record}})
    },
    apiSearch() {
      this.ApiSearchShow = !this.ApiSearchShow
      this.ApiPaginationOption.defaultCurrent = 1
    },
    apiEmptySearch() {
      this.ApiSearchDate = {}
      this.ApiPaginationOption.defaultCurrent = 1
    },
    async apiGets() {
      const {data: res} = await this.$http.post('apis', this.ApiSearchDate, {
        params: {
          pageNum: this.ApiPaginationOption.defaultCurrent,
          pageSize: this.ApiPaginationOption.defaultPageSize,
        }
      })
      this.ApiPaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.ApiData = res.data.apis
      this.ApiPaginationOption.total = res.data.total
      this.ApiSearchShow = false
    },
    apiSelectChange(selectedRowKeys) {
      console.log('ApiSelectedRowKeys changed: ', selectedRowKeys);
      this.ApiSelectedRowKeys = selectedRowKeys;
    },
    async apiExportDate() {
      const {data: res} = await this.$http.post('apis/exp', this.ApiSearchDate)
      if (res.code !== 200) return this.$message.warn(res.msg)
      let tableData = res.data.apis
      let ws = xlsx.utils.aoa_to_sheet(tableData)
      let wb = xlsx.utils.book_new()
      xlsx.utils.book_append_sheet(wb, ws, 'api') // 工作簿名称
      xlsx.writeFile(wb, 'api.xlsx') // 保存的文件名
    },
    apiPrintDate() {
      console.log('print data')
      this.ApiSelectedRowKeys.forEach((item, index) => {
        console.log(item, index)
      })
      this.ApiSelectedRowKeys = []
    },
    async apiInit() {
      this.ApiInitLoading = true
      const {data: res} = await this.$http.get('api/init')
      this.$message.success(`初始化完成，本次新增 ${res.data.insertNum} 个`)
      this.ApiInitLoading = false
      this.apiGets().catch((err) => err)
    },
  },
  filters: {
    filterTimeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    },
    apiTagColor(val) {
      const target = apiMethodOptions.filter(item => item.value === val)[0]
      return target.color
    },
    apiTagLabel(val) {
      const target = apiMethodOptions.filter(item => item.value === val)[0]
      return `${target.value} | ${target.label}`
    },
  }
};
</script>

<style>

.apiSearchRow > .ant-col, .apiSearchBtn {
  margin-top: 10px;
}

.ant-table-column-title {
  font-weight: bold;
}

#input-switch {
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa
}

</style>
