<template>
  <div>
    <a-card :title="ApiFormTitle">
      <a-form-model ref="ApiFormRef" :rules="ApiFormRules" :model="ApiFormData">
        <!--				<a-form-model-item hasFeedback prop="ID" label="ID">-->
        <!--					<a-input-number :disabled="ApiItemDisabled" v-model.number="ApiFormData.ID" />-->
        <!--				</a-form-model-item>-->
        <a-form-model-item hasFeedback prop="Path" label="路径">
          <a-input :disabled="ApiItemDisabled" v-model="ApiFormData.Path"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Method" label="方法">
          <a-select :disabled="ApiItemDisabled" v-model="ApiFormData.Method">
            <a-select-option
                v-for="item in apiMethodOptions"
                :key="item.value"
                :value="item.value"
            >
              <a-tag :color="item.color">{{ item.value }} | {{ item.label }}</a-tag>
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item label="菜单">
          <a-transfer
              :data-source="ApiMenuDataSource"
              :target-keys="ApiMenuDataTargetKeys"
              show-search
              :render="item => item.title"
              @change="apiMenuChange"
          />
        </a-form-model-item>
        <a-form-model-item class="apiFormBtn">
          <a-space>
            <a-button type="info" @click="apiFormReset">取消</a-button>
            <a-button type="primary" @click="apiFormOk">确定</a-button>
          </a-space>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
const apiMethodOptions = [
  {
    value: 'POST',
    label: '创建',
    color: 'green'
  },
  {
    value: 'GET',
    label: '查看',
    color: 'blue'
  },
  {
    value: 'PUT',
    label: '更新',
    color: 'cyan'
  },
  {
    value: 'DELETE',
    label: '删除',
    color: 'red'
  }
];

export default {
  name: "ApiDetail",
  data() {
    return {
      ApiFormTitle: '',
      ApiFormData: {},
      ApiFormRules: {
        Path: [
          {required: true, message: '路径不能为空', trigger: 'blur'},
        ],
        Method: [
          {required: true, message: '方法不能为空', trigger: 'blur'},
        ],
      },
      ApiItemDisabled: false,
      apiMethodOptions: apiMethodOptions,
      ApiMenuDataSource: [],
      ApiMenuDataTargetKeys: [],
    }
  },
  created() {
    this.initMenuDataSource()
    this.ApiFormTitle = this.$route.params.formTitle
    if (this.ApiFormTitle === 'Add Api') {
    } else if (this.ApiFormTitle === 'Update Api') {
      this.ApiFormData = this.$route.params.formData
      this.apiGet()
      this.ApiItemDisabled = true
    } else {
      this.$router.push({name: 'ApiAdmin'}).catch((err) => err)
    }
  },
  methods: {
    apiFormReset() {
      this.$refs.ApiFormRef.resetFields();
      this.$router.push({name: 'ApiAdmin'}).catch((err) => err)
    },
    apiFormOk() {
      if (this.ApiFormTitle === 'Add Api') {
        this.apiAdd()
      } else if (this.ApiFormTitle === 'Update Api') {
        this.apiUpdate()
      } else {
        this.$message.error('提交错误')
        this.$router.push({name: 'ApiAdmin'}).catch((err) => err)
      }
    },
    async apiAdd() {
      this.pushApiMenuChangeToForm()
      const {data: res} = await this.$http.post('api', this.ApiFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('新增成功')
      this.$router.push({name: 'ApiAdmin'}).catch((err) => err)
    },
    async apiUpdate() {
      this.pushApiMenuChangeToForm()
      const {data: res} = await this.$http.put('api/' + this.ApiFormData.ID, this.ApiFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'ApiAdmin'}).catch((err) => err)
    },
    async apiGet() {
      const {data: res} = await this.$http.get('api/' + this.ApiFormData.ID)
      if (res.code !== 200) return this.$message.warn(res.msg)
      if (res.data.api.Menu) {
        res.data.api.Menu.forEach((item) => {
          this.ApiMenuDataTargetKeys.push(item.ID.toString())
        })
      }
    },
    async initMenuDataSource() {
      const {data: res} = await this.$http.post('menus/mini')
      if (res.code !== 200) return this.$message.warn(res.msg)
      res.data.menus.forEach((item) => {
        this.ApiMenuDataSource.push({key: item.ID.toString(), title: item.Name})
      })
    },
    apiMenuChange(targetKeys, direction, moveKeys) {
      console.log(targetKeys, direction, moveKeys, 'apiMenuChange');
      this.ApiMenuDataTargetKeys = targetKeys;
    },
    pushApiMenuChangeToForm() {
      if (this.ApiMenuDataTargetKeys) {
        this.ApiFormData.Menu = []
        this.ApiMenuDataTargetKeys.forEach((item) => {
          this.ApiFormData.Menu.push({"id": parseInt(item)})
        })
      }
    },
  },
}
</script>

<style scoped>

.apiFormBtn {
  display: flex;
  justify-content: flex-end;
}

</style>
