<template>
    <div class="container">
        <div class="loginBox">
            <a-form-model ref="loginRef" :rules="rules" :model="formData" class="loginForm">
                <a-form-model-item hasFeedback prop="username">
                    <a-input v-model="formData.username" placeholder="username">
                        <a-icon slot="prefix" type="user" style="color:rgba(0,0,0,.25)"/>
                    </a-input>
                </a-form-model-item>
                <a-form-model-item hasFeedback prop="password">
                    <a-input v-model="formData.password" v-on:keyup.enter="login" type="password"
                             placeholder="password">
                        <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)"/>
                    </a-input>
                </a-form-model-item>
                <a-form-model-item class="loginBtn">
                    <a-checkbox @click="rememberLogin">记住登录</a-checkbox>
                    <a-button type="info" @click="resetForm">重置</a-button>
                    <a-button type="primary" @click="login">登录</a-button>
                </a-form-model-item>
            </a-form-model>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Login",
        data() {
            return {
                formData: {
                    username: 'eddie0',
                    password: 'eddie0',
                },
                rules: {
                    username: [
                        {required: true, message: '用户名不能为空', trigger: 'blur'},
                        {min: 6, max: 18, message: '请输入6~20位字母或数字', trigger: 'blur'},
                    ],
                    password: [
                        {required: true, message: '密码不能为空', trigger: 'blur'},
                        {min: 6, max: 18, message: '请输入6~20位字母或数字', trigger: 'blur'},
                    ],
                },
                rememberLoginFlag: true,
                // rememberLoginFlag: false,
            }
        },
        methods: {
            login() {
                this.$refs.loginRef.validate(async valid => {
                        if (!valid) return this.$message.warn('数据不合法，请重新输入')
                        const {data: res} = await this.$http.post('login', this.formData)
                        if (res.code !== 200) return this.$message.warn(res.msg)
                  console.log(res)
                        if (this.rememberLoginFlag) {
                            window.localStorage.setItem('zys-token', res.data.token)
                        } else {
                            window.sessionStorage.setItem('zys-token', res.data.token)
                        }
                        window.localStorage.setItem('username', res.data.loginUser)
                        window.localStorage.setItem('menus', res.data.menus)
                        this.$router.push('admin/index')
                    }
                )
            },
            resetForm() {
                this.$refs.loginRef.resetFields();
            },
            rememberLogin() {
                this.rememberLoginFlag = !this.rememberLoginFlag
            },
        },
        mounted() {
            //自动登录
            this.login()
        },
    }
</script>

<style scoped>
    .container {
        background-color: #282c33;
        height: 100%;

        background-image: url('../assets/bg.jpg');
        background-repeat: no-repeat;
        background-size: 100% 100%;
        background-attachment: fixed;
    }

    .loginBox {
        width: 380px;
        height: 260px;
        background-color: #ffffff;
        position: absolute;
        top: 40%;
        left: 85%;
        transform: translate(-50%, -50%);
        border-radius: 8px;
    }

    .loginForm {
        width: 100%;
        position: absolute;
        bottom: 10%;
        padding: 0 20px;
        box-sizing: border-box;
    }

    .loginBtn {
        display: flex;
        justify-content: flex-end;
    }

    .loginBtn >>> .ant-btn {
        margin-left: 10px;
    }
</style>