<template>
  <div>
    <a-row style="padding: 6px 6px 0px 6px" type="flex" justify="space-between" align="middle">
      <a-col span="6">
        <a-space>
          <a-button :type="isQuiet ? 'danger' : 'dashed'" :ghost="isQuiet ? true : false" icon="to-top">主赐大福</a-button>
          <a-button :type="isQuiet ? 'danger' : 'dashed'" :ghost="isQuiet ? true : false" icon="rise">金虎进宝</a-button>
          <a-button :type="isQuiet ? 'danger' : 'dashed'" :ghost="isQuiet ? true : false" icon="stock">瑞虎运财</a-button>
        </a-space>
      </a-col>
      <a-col span="1" style="display: flex; justify-content: flex-end">
        <router-link :to="{name:'StockAdmin'}">
          管理
        </router-link>
      </a-col>
      <a-col span="5" style="padding: 0 20px">
        <a-slider @afterChange="onAfterChange" v-model="timeLevel" :min="1" :max="100"/>
      </a-col>
      <a-col span="6">
        <table>
          <tr v-for="(item, key) in stockIndexes" :key="key">
            <td>
              <a-tag>{{ item.Now }}</a-tag>
            </td>
            <td>
              <a-tag :color="item.UpDown >= 0 ? 'red' : 'green'">{{ item.UpDown }}</a-tag>
            </td>
            <td>
              <a-tag>{{ item.Percent }}</a-tag>
            </td>
            <td>
              <a-tag>{{ item.VolumePercent }}</a-tag>
            </td>
          </tr>
        </table>
      </a-col>
      <a-col id="stockListenSwitch" span="6" style="display: flex; justify-content: flex-end">
        <a-switch checked-children="标准" un-checked-children="静默" v-model="isQuiet"/>
      </a-col>
    </a-row>
    <PriceListen1 v-for="(item, key) in prices" :key="key" :stock1="item" :isQuiet="isQuiet"></PriceListen1>
    <BackTop />
  </div>
</template>

<script>
import PriceListen1 from "../components/stock/PriceListen1";
import BackTop from "../ext_template/BackTop";

export default {
  name: "StockListen",
  components: {BackTop, PriceListen1},
  data() {
    return {
      timeLevel: 1,
      pricesNum: 28,
      priceShowTime: 5,
      prices: [],
      stockIndexes: [],
      isQuiet: false,
    }
  },
  methods: {
    onAfterChange() {
      this.$message.success(`间隔${this.timeLevel}秒`)
      clearInterval(this.timer)
      this.timer = setInterval(this.getPrices, 1000 * this.timeLevel)
    },

    clearOldData(idx) {
      // 使用 ? 号解决渲染未识别/未声明类型时的报错
      if (this.prices[idx]?.length >= this.priceShowTime) {
        // this.prices[idx].splice(0, 1)
        this.prices[idx].shift()
      }
    },

    async getPrices() {
      // console.log(convertQianweifu(123456))
      console.log('get new price.')
      // console.log(this.prices[0],"##2")
      const {data: res} = await this.$http.get('price/listens')
      this.stockIndexes = res.data.stockIndexes
      res.data.prices.forEach((item, index) => {
        this.clearOldData(index)
        // console.log(item.name, index)
        this.prices[index]?.push(item)
      })
    },
    async initStockListen() {
      const {data: res} = await this.$http.get('stock/listen/init')
      if (res.code !== 200) return this.$message.warn(`初始化监听失败，${res.msg}`)
    },
  },

  created() {
    for (let i = 0; i < this.pricesNum; i++) {
      let aaa = []
      console.log()
      this.prices.push(aaa)
    }
    this.initStockListen()
    this.timer = setInterval(this.getPrices, 1000 * this.timeLevel)
  },
  beforeDestroy() {
    clearInterval(this.timer)
  }

}
</script>

<style scoped>

td {
  width: 70px;
}

#stockListenSwitch .ant-switch-checked {
  background-color: palevioletred !important;
}

</style>