<template>
  <div>
    <a-card :title="StockFormTitle">
      <a-form-model ref="StockFormRef" :rules="StockFormRules" :model="StockFormData">
        <a-form-model-item hasFeedback prop="Name" label="名称">
          <a-input v-model="StockFormData.Name"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Code" label="代码">
          <a-input v-model="StockFormData.Code"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="PlateId" label="板块">
          <a-select v-model="StockFormData.PlateId">
            <a-select-option
                v-for="item in StockPlateOption"
                :key="item.ID"
                :value="item.ID"
            >
              {{ item.Name }}
            </a-select-option>
          </a-select>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Listen" label="盯盘">
          <a-switch v-model="StockFormData.Listen"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Hand" label="入手数">
          <a-input-number v-model.number="StockFormData.Hand"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="UpCall" label="涨提醒">
          <a-input-number v-model.number="StockFormData.UpCall"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="DownCall" label="跌提醒">
          <a-input-number v-model.number="StockFormData.DownCall"/>
        </a-form-model-item>
        <a-form-model-item class="stockFormBtn">
          <a-space>
            <a-button type="info" @click="stockFormReset">取消</a-button>
            <a-button type="primary" @click="stockFormOk">确定</a-button>
          </a-space>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
export default {
  name: "StockDetail",
  data() {
    return {
      StockFormTitle: '',
      StockFormData: {},
      StockFormRules: {
        Name: [
          {required: true, message: '名称不能为空', trigger: 'blur'},
        ],
        Code: [
          {required: true, message: '代码不能为空', trigger: 'blur'},
        ],
        Plate: [
          {required: true, message: 'Plate不能为空', trigger: 'blur'},
        ],
        PlateId: [
          {required: true, message: '板块不能为空', trigger: 'blur'},
        ],
        Listen: [],
        Hand: [
          {required: true, message: '入手数不能为空', trigger: 'blur'},
        ],
      },
      StockItemDisabled: false,
      StockPlateOption: [],
    }
  },
  created() {
    this.getStockPlateOption()
    this.StockFormTitle = this.$route.params.formTitle
    if (this.StockFormTitle === '新增_股票') {
      console.log('add')
      this.StockFormData.Listen = true
    } else if (this.StockFormTitle === '修改_股票') {
      this.StockFormData = this.$route.params.formData
      this.StockItemDisabled = true
    } else {
      this.$router.push({name: 'StockAdmin'}).catch((err) => err)
    }
  },
  methods: {
    async getStockPlateOption() {
      const {data: res} = await this.$http.get('enums/16')
      this.StockPlateOption = res.data.enum
    },
    stockFormReset() {
      this.$refs.StockFormRef.resetFields();
      this.$router.push({name: 'StockAdmin'}).catch((err) => err)
    },
    stockFormOk() {
      if (this.StockFormTitle === '新增_股票') {
        this.stockAdd()
      } else if (this.StockFormTitle === '修改_股票') {
        this.stockUpdate()
      } else {
        this.$message.error('提交错误')
        this.$router.push({name: 'StockAdmin'}).catch((err) => err)
      }
    },
    async stockAdd() {
      const {data: res} = await this.$http.post('stock', this.StockFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('新增成功')
      this.$router.push({name: 'StockAdmin'}).catch((err) => err)
    },
    async stockUpdate() {
      const {data: res} = await this.$http.put('stock/' + this.StockFormData.ID, this.StockFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'StockAdmin'}).catch((err) => err)
    },
  },
}
</script>

<style scoped>

.stockFormBtn {
  display: flex;
  justify-content: flex-end;
}

.ant-input-disabled, .ant-input-number-disabled {
  color: #000000A6 !important;
  background-color: #fafafa !important;
}

</style>
