<template>
  <div>
    <a-card :title="VersionFormTitle">
      <a-form-model ref="VersionFormRef" :rules="VersionFormRules" :model="VersionFormData">
        <a-form-model-item hasFeedback prop="Code" label="版本号">
          <a-input :disabled="VersionItemDisabled" v-model="VersionFormData.Code"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="UpdateLink" label="更新链接">
          <a-input :disabled="VersionItemDisabled" v-model="VersionFormData.UpdateLink"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Md5Hash" label="MD5校验值">
          <a-input :disabled="VersionItemDisabled" v-model="VersionFormData.Md5Hash"/>
        </a-form-model-item>
        <!--        <a-form-model-item hasFeedback prop="SoftwareId" label="软件ID">-->
        <!--          <a-input :disabled="VersionItemDisabled" v-model="VersionFormData.SoftwareId"/>-->
        <!--        </a-form-model-item>-->
        <a-form-model-item hasFeedback prop="UserId" label="用户ID">
          <a-input-number :disabled="VersionItemDisabled" v-model.number="VersionFormData.UserId"/>
        </a-form-model-item>
        <a-form-model-item class="versionFormBtn">
          <a-space>
            <a-button type="info" @click="versionFormReset">取消</a-button>
            <a-button type="primary" @click="versionFormOk">确定</a-button>
          </a-space>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
export default {
  name: "VersionDetail",
  data() {
    return {
      VersionFormTitle: '',
      VersionFormData: {},
      VersionFormRules: {
        Code: [
          {required: true, message: '版本号不能为空', trigger: 'blur'},
        ],
        UpdateLink: [
          {required: true, message: '更新链接不能为空', trigger: 'blur'},
        ],
        Md5Hash: [
          {required: true, message: 'SMd5Hash不能为空', trigger: 'blur'},
        ],
        SoftwareId: [
          {required: true, message: '软件ID不能为空', trigger: 'blur'},
        ],
        UserId: [
          {required: true, message: '用户ID不能为空', trigger: 'blur'},
        ],
      },
      VersionItemDisabled: false,
    }
  },
  created() {
    this.VersionFormTitle = this.$route.params.formTitle
    this.VersionFormData.SoftwareId = this.$route.params.softwareId
    // if (this.VersionFormTitle === '新增_版本') {
    //   console.log('add')
    // } else if (this.VersionFormTitle === '修改_版本') {
    //   this.VersionFormData = this.$route.params.formData
    //   this.VersionItemDisabled = true
    // } else {
    //   this.$router.push({name: 'VersionAdmin'}).catch((err) => err)
    // }
  },
  methods: {
    versionFormReset() {
      this.$refs.VersionFormRef.resetFields();
      this.$router.push({name: 'SoftwareAdmin'}).catch((err) => err)
    },
    versionFormOk() {
      this.versionAdd()
      // if (this.VersionFormTitle === '新增_版本') {
      //   this.versionAdd()
      // } else if (this.VersionFormTitle === '修改_版本') {
      //   this.versionUpdate()
      // } else {
      //   this.$message.error('提交错误')
      //   this.$router.push({name: 'VersionAdmin'}).catch((err) => err)
      // }
    },
    async versionAdd() {
      const {data: res} = await this.$http.post('version', this.VersionFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('新增成功')
      this.$router.push({
        name: 'SoftwareAdmin',
        // name: 'VersionAdmin',
        // params: {Software: {ID: this.VersionFormData.SoftwareId}}
      }).catch((err) => err)
    },
    async versionUpdate() {
      const {data: res} = await this.$http.put('version/' + this.VersionFormData.ID, this.VersionFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'VersionAdmin'}).catch((err) => err)
    },
  },
}
</script>

<style scoped>

.versionFormBtn {
  display: flex;
  justify-content: flex-end;
}

.ant-input-disabled, .ant-input-number-disabled {
  color: #000000A6 !important;
  background-color: #fafafa !important;
}

</style>
