<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="connectInfoSearch">筛选</a-button>
              <a-button type="primary" @click="connectInfoAdd">新增</a-button>
              <a-button type="primary" :disabled="!connectInfoHasSelected" @click="connectInfoDel">删除</a-button>
              <span style="margin-left: 8px">
								<template v-if="connectInfoHasSelected">
									已选中 {{ ConnectInfoSelectedRowKeys.length }} 行
								</template>
							</span>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button @click="connectInfoExportDate">导出</a-button>
              <a-button :disabled="!connectInfoHasSelected" @click="connectInfoPrintDate">打印</a-button>
            </a-space>
          </a-col>
        </a-row>
        <div v-if="ConnectInfoSearchShow">
          <a-row :gutter="[12,8]" class="connectInfoSearchInput">
            <a-col span="4">
              <a-input v-model="ConnectInfoSearchDate.Ip" addon-before="地址"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="ConnectInfoSearchDate.Port" addon-before="端口"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="ConnectInfoSearchDate.Type" addon-before="类型"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="ConnectInfoSearchDate.Name" addon-before="名称"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="ConnectInfoSearchDate.User" addon-before="用户"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="ConnectInfoSearchDate.Password" addon-before="密码"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="ConnectInfoSearchDate.Schemas" addon-before="实例"/>
            </a-col>
          </a-row>
          <a-row class="connectInfoSearchBtn">
            <a-space size="large">
              <a-button type="primary" @click="connectInfoEmptySearch">重置</a-button>
              <a-button type="primary" @click="connectInfoGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table
          :row-selection="{ selectedRowKeys: ConnectInfoSelectedRowKeys, onChange: connectInfoSelectChange }"
          :columns="ConnectInfoColumns"
          :data-source="ConnectInfoData"
          rowKey="ID"
          :pagination="ConnectInfoPaginationOption"
          bordered
          size="small"
      >
        <span slot="Password" slot-scope="Password">
<!--          <a-input v-model="Password" disabled type="password" />-->
          ***
        </span>
        <span slot="connectInfoDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="connectInfoRecordUpdate(record)">修改</a-button>
          </a-space>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import xlsx from 'xlsx'
import moment from 'moment'

const ConnectInfoColumns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.ID - b.ID,
    scopedSlots: {customRender: 'ID'},
  },
  {
    title: '地址',
    dataIndex: 'Ip',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Ip - b.Ip,
    scopedSlots: {customRender: 'Ip'},
  },
  {
    title: '端口',
    dataIndex: 'Port',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Port - b.Port,
    scopedSlots: {customRender: 'Port'},
  },
  {
    title: '类型',
    dataIndex: 'Type',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Type - b.Type,
    scopedSlots: {customRender: 'Type'},
  },
  {
    title: '名称',
    dataIndex: 'Name',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Name - b.Name,
    scopedSlots: {customRender: 'Name'},
  },
  {
    title: '用户',
    dataIndex: 'User',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.User - b.User,
    scopedSlots: {customRender: 'User'},
  },
  {
    title: '密码',
    dataIndex: 'Password',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Password - b.Password,
    scopedSlots: {customRender: 'Password'},
  },
  {
    title: '实例',
    dataIndex: 'Schemas',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Schemas - b.Schemas,
    scopedSlots: {customRender: 'Schemas'},
  },
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'connectInfoDoSome'},
  },
];
export default {
  name: "ConnectInfoAdmin",
  data() {
    return {
      ConnectInfoData: [],
      ConnectInfoColumns,
      ConnectInfoSelectedRowKeys: [],
      ConnectInfoSearchDate: {},
      ConnectInfoSearchShow: false,
      ConnectInfoPaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 10,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.ConnectInfoPaginationOption.defaultCurrent = current
          this.ConnectInfoPaginationOption.defaultPageSize = pageSize
          this.connectInfoGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.ConnectInfoPaginationOption.defaultCurrent = current
          this.ConnectInfoPaginationOption.defaultPageSize = pageSize
          this.connectInfoGets()
        },
      },
    };
  },
  computed: {
    connectInfoHasSelected() {
      return this.ConnectInfoSelectedRowKeys.length > 0;
    },
  },
  created() {
    this.connectInfoGets()
  },
  methods: {
    connectInfoAdd() {
      this.$router.push({name: 'ConnectInfoDetail', params: {formTitle: '新增_数据库连接'}}).catch((err) => err)
    },
    connectInfoDel() {
      if (this.ConnectInfoSelectedRowKeys.length > 1) return this.$message.warn('不允许删除多行')
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.connectInfoDelOk()
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async connectInfoDelOk() {
      const {data: res} = await this.$http.delete('connect_info/' + this.ConnectInfoSelectedRowKeys)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.ConnectInfoSelectedRowKeys = []
      this.connectInfoGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    connectInfoRecordUpdate(record) {
      this.$router.push({name: 'ConnectInfoDetail', params: {formTitle: '修改_数据库连接', formData: record}})
    },
    connectInfoSearch() {
      this.ConnectInfoSearchShow = !this.ConnectInfoSearchShow
      this.ConnectInfoPaginationOption.defaultCurrent = 1
    },
    connectInfoEmptySearch() {
      this.ConnectInfoSearchDate = {}
      this.ConnectInfoPaginationOption.defaultCurrent = 1
    },
    async connectInfoGets() {
      const {data: res} = await this.$http.post('connect_info/list', this.ConnectInfoSearchDate, {
        params: {
          pageNum: this.ConnectInfoPaginationOption.defaultCurrent,
          pageSize: this.ConnectInfoPaginationOption.defaultPageSize,
        }
      })
      this.ConnectInfoPaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.ConnectInfoData = res.data.connectInfoList
      this.ConnectInfoPaginationOption.total = res.data.total
      this.ConnectInfoSearchShow = false
    },
    connectInfoSelectChange(selectedRowKeys) {
      console.log('ConnectInfoSelectedRowKeys changed: ', selectedRowKeys);
      this.ConnectInfoSelectedRowKeys = selectedRowKeys;
    },
    async connectInfoExportDate() {
      const {data: res} = await this.$http.post('connect_info/list/exp', this.ConnectInfoSearchDate)
      if (res.code !== 200) return this.$message.warn(res.msg)
      let tableData = res.data.connectInfoList
      let ws = xlsx.utils.aoa_to_sheet(tableData)
      let wb = xlsx.utils.book_new()
      xlsx.utils.book_append_sheet(wb, ws, 'connect_info') // 工作簿名称
      xlsx.writeFile(wb, 'connect_info.xlsx') // 保存的文件名
    },
    connectInfoPrintDate() {
      console.log('print data')
      this.ConnectInfoSelectedRowKeys.forEach((item, index) => {
        console.log(item, index)
      })
      this.ConnectInfoSelectedRowKeys = []
    },
  },
  filters: {
    timeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    }
  }
};
</script>

<style>

.connectInfoSearchBtn {
  text-align: center;
}

.connectInfoSearchInput, .connectInfoSearchBtn {
  margin-top: 10px !important;
}

.ant-table-column-title {
  font-weight: bold;
}

.ant-input-disabled, .ant-input-number-disabled {
  color: #000000A6 !important;
  background-color: #fafafa !important;
}

</style>
