<template>
  <div>
    <a-form-model ref="changePasswordRef" :rules="rules" :model="changePasswordForm" class="changePasswordForm">
      <a-form-model-item hasFeedback prop="password">
        <a-input v-model="changePasswordForm.password" type="password" placeholder="请输入新密码">
          <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)"/>
        </a-input>
      </a-form-model-item>
      <a-form-model-item hasFeedback prop="passwordAgain">
        <a-input v-model="changePasswordForm.passwordAgain" v-on:keyup.enter="changePassword" type="password"
                 placeholder="请再次输入新密码">
          <a-icon slot="prefix" type="lock" style="color:rgba(0,0,0,.25)"/>
        </a-input>
      </a-form-model-item>
      <a-form-model-item class="changePasswordBtn">
        <a-space>
          <a-button type="info" @click="resetForm">取消</a-button>
          <a-button type="primary" @click="changePassword">修改</a-button>
        </a-space>
      </a-form-model-item>
    </a-form-model>
  </div>
</template>

<script>
export default {
  name: "ChangePassword",
  props: ['showModal'],
  data() {
    return {
      show: false,
      changePasswordForm: {
        password: '',
        passwordAgain: '',
      },
      rules: {
        password: [
          {required: true, message: '密码不能为空', trigger: 'blur'},
          {min: 6, max: 18, message: '请输入6~20位字母或数字', trigger: 'blur'},
        ],
        passwordAgain: [
          {required: true, message: '密码不能为空', trigger: 'blur'},
          {min: 6, max: 18, message: '请输入6~20位字母或数字', trigger: 'blur'},
        ],
      },
    }
  },
  // created() {
  //   this.show=this.modelShow
  // },
  methods: {
    changePassword() {
      this.$refs.changePasswordRef.validate(async valid => {
            if (!valid) return this.$message.warn('数据不合法，请重新输入')
            if (this.changePasswordForm.password !== this.changePasswordForm.passwordAgain) {
              return this.$message.warn('两次密码不一致')
            }
            const {data: res} = await this.$http.put('user/password', this.changePasswordForm)
            if (res.code !== 200) return this.$message.warn(res.msg)
            this.$router.push({name: 'Login'})
          }
      )
    },
    resetForm() {
      this.showModal(false);
      this.$refs.changePasswordRef.resetFields();
    },
  },
}
</script>

<style scoped>

.changePasswordBtn {
  display: flex;
  justify-content: flex-end;
}

</style>