<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="enumSearch">筛选</a-button>
              <a-button type="primary" @click="enumAdd">新增</a-button>
              <a-button type="primary" :disabled="!enumHasSelected" @click="enumDel">删除</a-button>
              <span style="margin-left: 8px">
								<template v-if="enumHasSelected">
									已选中 {{ EnumSelectedRowKeys.length }} 行
								</template>
							</span>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button @click="enumExportDate">导出</a-button>
              <a-button :disabled="!enumHasSelected" @click="enumPrintDate">打印</a-button>
            </a-space>
          </a-col>
        </a-row>
        <!--        <div v-if="EnumSearchShow">-->
        <div v-if="true">
          <a-row :gutter="6" class="enumSearchRow">
            <a-col span="4">
              <a-input v-model.number="EnumSearchDate.PID" addon-before="PID"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="EnumSearchDate.Name" addon-before="名称"/>
            </a-col>
            <a-col span="4"  id="col-input-switch">
              <a-input id="input-switch" disabled defaultValue="启用">
                <a-switch v-model="EnumSearchDate.IsEnable" slot="addonAfter"/>
              </a-input>
            </a-col>
          </a-row>
          <a-row class="enumSearchBtn" style="text-align: center;">
            <a-space size="large">
              <a-button type="primary" @click="enumEmptySearch">重置</a-button>
              <a-button type="primary" @click="enumGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table
          :row-selection="{ selectedRowKeys: EnumSelectedRowKeys, onChange: enumSelectChange }"
          :columns="EnumColumns"
          :data-source="EnumData"
          rowKey="ID"
          :pagination="EnumPaginationOption"
          bordered
          size="small"
      >
                <span slot="IsEnable" slot-scope="IsEnable">
                    <a-icon v-if="IsEnable" type="check" style="color: green"/>
                    <a-icon v-if="!IsEnable" type="close" style="color: red"/>
                </span>
        <span slot="enumDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="enumRecordUpdate(record)">修改</a-button>
            <a-button size="small" @click="enumChild(record)">子项</a-button>
          </a-space>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import xlsx from 'xlsx'
import moment from 'moment'

const EnumColumns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.ID - b.ID,
    scopedSlots: {customRender: 'ID'},
  },
  {
    title: 'PID',
    dataIndex: 'PID',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.PID - b.PID,
    scopedSlots: {customRender: 'PID'},
  },
  {
    title: '名称',
    dataIndex: 'Name',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Name - b.Name,
    scopedSlots: {customRender: 'Name'},
  },
  {
    title: '枚举值',
    dataIndex: 'Val',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Val - b.Val,
    scopedSlots: {customRender: 'Val'},
  },
  {
    title: '排序',
    dataIndex: 'Sort',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Sort - b.Sort,
    scopedSlots: {customRender: 'Sort'},
  },
  {
    title: '启用',
    dataIndex: 'IsEnable',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.IsEnable - b.IsEnable,
    scopedSlots: {customRender: 'IsEnable'},
  },
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'enumDoSome'},
  },
];
export default {
  name: "EnumAdmin",
  data() {
    return {
      EnumData: [],
      EnumColumns,
      EnumSelectedRowKeys: [],
      EnumSearchDate: {},
      EnumSearchShow: false,
      EnumPaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 10,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.EnumPaginationOption.defaultCurrent = current
          this.EnumPaginationOption.defaultPageSize = pageSize
          this.enumGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.EnumPaginationOption.defaultCurrent = current
          this.EnumPaginationOption.defaultPageSize = pageSize
          this.enumGets()
        },
      },
    };
  },
  computed: {
    enumHasSelected() {
      return this.EnumSelectedRowKeys.length > 0;
    },
  },
  created() {
    this.enumEmptySearch()
  },
  methods: {
    enumRecordUpdate(record) {
      this.$router.push({name: 'EnumDetail', params: {formTitle: 'Update Enum', formData: record}})
    },
    enumSearch() {
      this.EnumSearchShow = !this.EnumSearchShow
      this.EnumPaginationOption.defaultCurrent = 1
    },
    enumEmptySearch() {
      this.EnumSearchDate = {
        IsEnable: true,
        PID: 0,
      }
      this.EnumPaginationOption.defaultCurrent = 1
      this.enumGets()
    },
    async enumGets() {
      const {data: res} = await this.$http.post('enums', this.EnumSearchDate, {
        params: {
          pageNum: this.EnumPaginationOption.defaultCurrent,
          pageSize: this.EnumPaginationOption.defaultPageSize,
        }
      })
      this.EnumPaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.EnumData = res.data.enums
      this.EnumPaginationOption.total = res.data.total
      this.EnumSearchShow = false
    },
    enumAdd() {
      this.$router.push({
        name: 'EnumDetail',
        params: {formTitle: 'Add Enum', pid: this.EnumSearchDate.PID}
      }).catch((err) => err)
    },
    enumChild(record) {
      this.EnumSearchDate.PID = record.ID
      this.enumGets()
      // this.$router.push({name: 'EnumDetail', params: {formTitle: 'Add Enum', pid: record.ID}}).catch((err) => err)
    },
    enumDel() {
      if (this.EnumSelectedRowKeys.length > 1) return this.$message.warn('不允许删除多行')
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.enumDelOk()
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async enumDelOk() {
      const {data: res} = await this.$http.delete('enum/' + this.EnumSelectedRowKeys)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.EnumSelectedRowKeys = []
      this.enumGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    enumSelectChange(selectedRowKeys) {
      console.log('EnumSelectedRowKeys changed: ', selectedRowKeys);
      this.EnumSelectedRowKeys = selectedRowKeys;
    },
    async enumExportDate() {
      const {data: res} = await this.$http.post('enums/exp', this.EnumSearchDate)
      if (res.code !== 200) return this.$message.warn(res.msg)
      let tableData = res.data.enums
      let ws = xlsx.utils.aoa_to_sheet(tableData)
      let wb = xlsx.utils.book_new()
      xlsx.utils.book_append_sheet(wb, ws, 'enum') // 工作簿名称
      xlsx.writeFile(wb, 'enum.xlsx') // 保存的文件名
    },
    enumPrintDate() {
      console.log('print data')
      this.EnumSelectedRowKeys.forEach((item, index) => {
        console.log(item, index)
      })
      this.EnumSelectedRowKeys = []
    },
  },
  filters: {
    filterTimeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    }
  }
};
</script>

<style>

.enumSearchRow > .ant-col, .enumSearchBtn {
  margin-top: 10px;
}

.ant-table-column-title {
  font-weight: bold;
}

#input-switch {
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa
}

#col-input-switch .ant-input-group-addon {
  background-color: #ffffff;
}

</style>
