<template>
  <div>
    <a-card :title="RoleFormTitle">
      <a-form-model ref="RoleFormRef" :rules="RoleFormRules" :model="RoleFormData">
        <!--				<a-form-model-item hasFeedback prop="ID" label="ID">-->
        <!--					<a-input-number :disabled="RoleItemDisabled" v-model.number="RoleFormData.ID" />-->
        <!--				</a-form-model-item>-->
        <a-form-model-item hasFeedback prop="Name" label="Name">
          <a-input v-model="RoleFormData.Name"/>
        </a-form-model-item>
        <a-form-model-item class="roleFormBtn">
          <a-space>
            <a-button type="info" @click="roleFormReset">取消</a-button>
            <a-button type="primary" @click="roleFormOk">确定</a-button>
          </a-space>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
export default {
  name: "RoleDetail",
  data() {
    return {
      RoleFormTitle: '',
      RoleFormData: {},
      RoleFormRules: {
        ID: [
          {required: true, message: 'ID不能为空', trigger: 'blur'},
        ],
        Name: [
          {required: true, message: 'Name不能为空', trigger: 'blur'},
        ],
        User: [
          {required: true, message: 'User不能为空', trigger: 'blur'},
        ],
        Api: [
          {required: true, message: 'Api不能为空', trigger: 'blur'},
        ],
      },
      RoleItemDisabled: false,
    }
  },
  created() {
    this.RoleFormTitle = this.$route.params.formTitle
    if (this.RoleFormTitle === 'Add Role') {
    } else if (this.RoleFormTitle === 'Update Role') {
      this.RoleFormData = this.$route.params.formData
      this.RoleItemDisabled = true
    } else {
      this.$router.push({name: 'RoleAdmin'}).catch((err) => err)
    }
  },
  methods: {
    roleFormReset() {
      this.$refs.RoleFormRef.resetFields();
      this.$router.push({name: 'RoleAdmin'}).catch((err) => err)
    },
    roleFormOk() {
      if (this.RoleFormTitle === 'Add Role') {
        this.roleAdd()
      } else if (this.RoleFormTitle === 'Update Role') {
        this.roleUpdate()
      } else {
        this.$message.error('提交错误')
        this.$router.push({name: 'RoleAdmin'}).catch((err) => err)
      }
    },
    async roleAdd() {
      const {data: res} = await this.$http.post('role', this.RoleFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('新增成功')
      this.$router.push({name: 'RoleAdmin'}).catch((err) => err)
    },
    async roleUpdate() {
      const {data: res} = await this.$http.put('role/' + this.RoleFormData.ID, this.RoleFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'RoleAdmin'}).catch((err) => err)
    },
  },
}
</script>

<style scoped>

.roleFormBtn {
  display: flex;
  justify-content: flex-end;
}

</style>
