<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="stockSearch">筛选</a-button>
              <a-button type="primary" @click="stockAdd">新增</a-button>
              <a-button type="primary" :disabled="!stockHasSelected" @click="stockDel">删除</a-button>
              <span style="margin-left: 8px">
								<template v-if="stockHasSelected">
									已选中 {{ StockSelectedRowKeys.length }} 行
								</template>
							</span>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button type="danger" @click="stockListen">盯盘</a-button>
              <!--              <a-button @click="stockExportDate">导出</a-button>-->
              <!--              <a-button :disabled="!stockHasSelected" @click="stockPrintDate">打印</a-button>-->
            </a-space>
          </a-col>
        </a-row>
        <div v-if="StockSearchShow">
          <a-row :gutter="[12,8]" class="stockSearchInput">
            <a-col span="4">
              <a-input v-model="StockSearchDate.Name" addon-before="名称"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="StockSearchDate.Code" addon-before="代码"/>
            </a-col>
<!--            <a-col span="4">-->
<!--              <a-input disabled defaultValue="盯盘">-->
<!--                <a-switch v-model="StockSearchDate.Listen" slot="addonAfter"/>-->
<!--              </a-input>-->
<!--            </a-col>-->
<!--            <a-col span="4">-->
<!--              <a-input v-model.number="StockSearchDate.Hand" addon-before="入手数"/>-->
<!--            </a-col>-->
          </a-row>
          <a-row class="stockSearchBtn">
            <a-space size="large">
              <a-button type="primary" @click="stockEmptySearch">重置</a-button>
              <a-button type="primary" @click="stockGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table id="stockAdminTable"
          :row-selection="{ selectedRowKeys: StockSelectedRowKeys, onChange: stockSelectChange }"
          :columns="StockColumns"
          :data-source="StockData"
          rowKey="ID"
          :pagination="StockPaginationOption"
          bordered
          size="small"
      >
				<span slot="Listen" slot-scope="Listen, record">
            <a-switch :checked="Listen" @change="listenChange(record)">
              <a-icon slot="checkedChildren" type="check"/>
              <a-icon slot="unCheckedChildren" type="close"/>
            </a-switch>
				</span>
        <span slot="Hand" slot-scope="Hand, record">
          <a-input-number v-model="Hand" @change="handChange(Hand, record)"/>
        </span>
        <span slot="UpCall" slot-scope="UpCall, record">
          <a-input-number v-model="UpCall" step="0.5" @change="upCallChange(UpCall, record)"/>
        </span>
        <span slot="DownCall" slot-scope="DownCall, record">
          <a-input-number v-model="DownCall" step="0.5" @change="downCallChange(DownCall, record)"/>
        </span>
        <span slot="Plate" slot-scope="Plate">
          {{ Plate.Name }}
        </span>
        <span slot="stockDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="stockRecordUpdate(record)">修改</a-button>
          </a-space>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import xlsx from 'xlsx'
import moment from 'moment'

const StockColumns = [
  // {
  //   title: 'ID',
  //   dataIndex: 'ID',
  //   align: 'center',
  //   sortDirections: ['descend', 'ascend'],
  //   sorter: (a, b) => a.ID - b.ID,
  //   scopedSlots: {customRender: 'ID'},
  // },
  {
    title: '名称',
    dataIndex: 'Name',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Name.localeCompare(b.Name),
    scopedSlots: {customRender: 'Name'},
  },
  {
    title: '代码',
    dataIndex: 'Code',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Code - b.Code,
    scopedSlots: {customRender: 'Code'},
  },
  {
    title: '板块',
    dataIndex: 'Plate',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.PlateId - b.PlateId,
    scopedSlots: {customRender: 'Plate'},
  },
  {
    title: '盯盘',
    dataIndex: 'Listen',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Listen - b.Listen,
    scopedSlots: {customRender: 'Listen'},
  },
  {
    title: '入手数',
    dataIndex: 'Hand',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Hand - b.Hand,
    scopedSlots: {customRender: 'Hand'},
  },
  {
    title: '涨提醒',
    dataIndex: 'UpCall',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.UpCall - b.UpCall,
    scopedSlots: {customRender: 'UpCall'},
  },
  {
    title: '跌提醒',
    dataIndex: 'DownCall',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.DownCall - b.DownCall,
    scopedSlots: {customRender: 'DownCall'},
  },
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'stockDoSome'},
  },
];
export default {
  name: "StockAdmin",
  data() {
    return {
      StockData: [],
      StockColumns,
      StockSelectedRowKeys: [],
      StockSearchDate: {},
      StockSearchShow: false,
      StockPaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 20,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.StockPaginationOption.defaultCurrent = current
          this.StockPaginationOption.defaultPageSize = pageSize
          this.stockGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.StockPaginationOption.defaultCurrent = current
          this.StockPaginationOption.defaultPageSize = pageSize
          this.stockGets()
        },
      },
      StockPlateOption: [],
    };
  },
  computed: {
    stockHasSelected() {
      return this.StockSelectedRowKeys.length > 0;
    },
  },
  created() {
    this.stockGets()
  },
  methods: {
    async listenChange(record) {
      record.Listen = !record.Listen
      const {data: res} = await this.$http.put('stock/' + record.ID, record)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success(this.isUsed(record.Listen))
    },
    isUsed(b) {
      return b ? '启用' : '停用'
    },
    async handChange(Hand, record) {
      record.Hand = Hand
      const {data: res} = await this.$http.put('stock/' + record.ID, record)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success(`${Hand}手`)
    },
    async upCallChange(UpCall, record) {
      record.UpCall = UpCall
      const {data: res} = await this.$http.put('stock/' + record.ID, record)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success(`涨超${UpCall}`)
    },
    async downCallChange(DownCall, record) {
      record.DownCall = DownCall
      const {data: res} = await this.$http.put('stock/' + record.ID, record)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success(`跌至${DownCall}`)
    },
    stockListen() {
      this.$router.push({name: 'StockListen'})
    },
    stockAdd() {
      this.$router.push({name: 'StockDetail', params: {formTitle: '新增_股票'}}).catch((err) => err)
    },
    stockDel() {
      if (this.StockSelectedRowKeys.length > 1) return this.$message.warn('不允许删除多行')
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.stockDelOk()
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async stockDelOk() {
      const {data: res} = await this.$http.delete('stock/' + this.StockSelectedRowKeys)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.StockSelectedRowKeys = []
      this.stockGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    stockRecordUpdate(record) {
      this.$router.push({name: 'StockDetail', params: {formTitle: '修改_股票', formData: record}})
    },
    stockSearch() {
      this.StockSearchShow = !this.StockSearchShow
      this.StockPaginationOption.defaultCurrent = 1
    },
    stockEmptySearch() {
      this.StockSearchDate = {}
      this.StockPaginationOption.defaultCurrent = 1
      this.stockGets()
    },
    async stockGets() {
      const {data: res} = await this.$http.post('stock/list', this.StockSearchDate, {
        params: {
          pageNum: this.StockPaginationOption.defaultCurrent,
          pageSize: this.StockPaginationOption.defaultPageSize,
        }
      })
      this.StockPaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.StockData = res.data.stockList
      this.StockPaginationOption.total = res.data.total
      this.StockSearchShow = false
    },
    stockSelectChange(selectedRowKeys) {
      console.log('StockSelectedRowKeys changed: ', selectedRowKeys);
      this.StockSelectedRowKeys = selectedRowKeys;
    },
    async stockExportDate() {
      const {data: res} = await this.$http.post('stock/list/exp', this.StockSearchDate)
      if (res.code !== 200) return this.$message.warn(res.msg)
      let tableData = res.data.stockList
      let ws = xlsx.utils.aoa_to_sheet(tableData)
      let wb = xlsx.utils.book_new()
      xlsx.utils.book_append_sheet(wb, ws, 'stock') // 工作簿名称
      xlsx.writeFile(wb, 'stock.xlsx') // 保存的文件名
    },
    stockPrintDate() {
      console.log('print data')
      this.StockSelectedRowKeys.forEach((item, index) => {
        console.log(item, index)
      })
      this.StockSelectedRowKeys = []
    },
  },
  filters: {
    timeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    },
  }
};
</script>

<style>

.stockSearchBtn {
  text-align: center;
}

.stockSearchInput, .stockSearchBtn {
  margin-top: 10px !important;
}

.ant-table-column-title {
  font-weight: bold;
}

.ant-input-disabled, .ant-input-number-disabled {
  color: #000000A6 !important;
  background-color: #fafafa !important;
}

#stockAdminTable .ant-input-number-input{
  text-align: right!important;
  padding-right: 2em!important;
}

</style>
