<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="roleSearch">筛选</a-button>
              <a-button type="primary" @click="roleAdd">新增</a-button>
              <a-button type="primary" :disabled="!roleHasSelected" @click="roleDel">删除</a-button>
              <span style="margin-left: 8px">
								<template v-if="roleHasSelected">
									已选中 {{ RoleSelectedRowKeys.length }} 行
								</template>
							</span>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button @click="roleExportDate">导出</a-button>
              <a-button :disabled="!roleHasSelected" @click="rolePrintDate">打印</a-button>
            </a-space>
          </a-col>
        </a-row>
        <!--        <div v-if="RoleSearchShow">-->
        <div v-if="true">
          <a-row :gutter="6" class="roleSearchRow">
            <a-col span="4">
              <a-input v-model="RoleSearchDate.Name" addon-before="Name"/>
            </a-col>
          </a-row>
          <a-row class="roleSearchBtn" style="text-align: center;">
            <a-space size="large">
              <a-button type="primary" @click="roleEmptySearch">重置</a-button>
              <a-button type="primary" @click="roleGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table
          :row-selection="{ selectedRowKeys: RoleSelectedRowKeys, onChange: roleSelectChange }"
          :columns="RoleColumns"
          :data-source="RoleData"
          rowKey="ID"
          :pagination="RolePaginationOption"
          bordered
          size="small"
      >
        <span slot="roleDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="roleRecordUpdate(record)">修改</a-button>
            <a-button type="danger" size="small" @click="roleApiPut(record)">权限</a-button>
          </a-space>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import xlsx from 'xlsx'
import moment from 'moment'

const RoleColumns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.ID - b.ID,
    scopedSlots: {customRender: 'ID'},
  },
  {
    title: 'Name',
    dataIndex: 'Name',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Name - b.Name,
    scopedSlots: {customRender: 'Name'},
  },
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'roleDoSome'},
  },
];
export default {
  name: "RoleAdmin",
  data() {
    return {
      RoleData: [],
      RoleColumns,
      RoleSelectedRowKeys: [],
      RoleSearchDate: {},
      RoleSearchShow: false,
      RolePaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 10,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.RolePaginationOption.defaultCurrent = current
          this.RolePaginationOption.defaultPageSize = pageSize
          this.roleGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.RolePaginationOption.defaultCurrent = current
          this.RolePaginationOption.defaultPageSize = pageSize
          this.roleGets()
        },
      },
    };
  },
  computed: {
    roleHasSelected() {
      return this.RoleSelectedRowKeys.length > 0;
    },
  },
  created() {
    this.roleGets()
  },
  methods: {
    roleAdd() {
      this.$router.push({name: 'RoleDetail', params: {formTitle: 'Add Role'}}).catch((err) => err)
    },
    roleDel() {
      if (this.RoleSelectedRowKeys.length > 1) return this.$message.warn('不允许删除多行')
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.roleDelOk()
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async roleDelOk() {
      const {data: res} = await this.$http.delete('role/' + this.RoleSelectedRowKeys)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.RoleSelectedRowKeys = []
      this.roleGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    roleRecordUpdate(record) {
      this.$message.warn('更名将会清空权限，请慎重')
      this.$router.push({name: 'RoleDetail', params: {formTitle: 'Update Role', formData: record}})
    },
    roleSearch() {
      this.RoleSearchShow = !this.RoleSearchShow
      this.RolePaginationOption.defaultCurrent = 1
    },
    roleEmptySearch() {
      this.RoleSearchDate = {}
      this.RolePaginationOption.defaultCurrent = 1
    },
    async roleGets() {
      const {data: res} = await this.$http.post('roles', this.RoleSearchDate, {
        params: {
          pageNum: this.RolePaginationOption.defaultCurrent,
          pageSize: this.RolePaginationOption.defaultPageSize,
        }
      })
      this.RolePaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.RoleData = res.data.roles
      this.RolePaginationOption.total = res.data.total
      this.RoleSearchShow = false
    },
    roleSelectChange(selectedRowKeys) {
      console.log('RoleSelectedRowKeys changed: ', selectedRowKeys);
      this.RoleSelectedRowKeys = selectedRowKeys;
    },
    async roleExportDate() {
      const {data: res} = await this.$http.post('roles/exp', this.RoleSearchDate)
      if (res.code !== 200) return this.$message.warn(res.msg)
      let tableData = res.data.roles
      let ws = xlsx.utils.aoa_to_sheet(tableData)
      let wb = xlsx.utils.book_new()
      xlsx.utils.book_append_sheet(wb, ws, 'role') // 工作簿名称
      xlsx.writeFile(wb, 'role.xlsx') // 保存的文件名
    },
    rolePrintDate() {
      console.log('print data')
      this.RoleSelectedRowKeys.forEach((item, index) => {
        console.log(item, index)
      })
      this.RoleSelectedRowKeys = []
    },
    roleApiPut(item) {
      this.$router.push({name: 'RoleApi', params: {item: item}})
    },
  },
  filters: {
    filterTimeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    }
  }
};
</script>

<style>

.roleSearchRow > .ant-col, .roleSearchBtn {
  margin-top: 10px;
}

.ant-table-column-title {
  font-weight: bold;
}

#input-switch {
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa
}

</style>
