<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="apiSearch">筛选</a-button>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button-group>
                <a-button type="default">{{ RoleItem.Name }}</a-button>
                <a-button type="danger" @click="roleApiPut" :loading="RoleApiPutLoading">保存权限</a-button>
              </a-button-group>
            </a-space>
          </a-col>
        </a-row>
        <!--        <div v-if="ApiSearchShow">-->
        <div v-if="true">
          <a-row :gutter="6" class="apiSearchRow">
            <a-col span="4">
              <a-input v-model="ApiSearchDate.Path" addon-before="路径"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="ApiSearchDate.Method" addon-before="方法"/>
            </a-col>
          </a-row>
          <a-row class="apiSearchBtn" style="text-align: center;">
            <a-space size="large">
              <a-button type="primary" @click="apiEmptySearch">重置</a-button>
              <a-button type="primary" @click="apiGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table
          :columns="ApiColumns"
          :data-source="ApiData"
          rowKey="ID"
          :pagination="ApiPaginationOption"
          bordered
          size="small"
      >
        <span slot="apiDoSome" slot-scope="record">
          <a-space size="small">
            <a-switch :checked="apiHasSelected(record.ID)" @change="roleApiSwitchChange(record)">
              <a-icon slot="checkedChildren" type="check"/>
              <a-icon slot="unCheckedChildren" type="close"/>
            </a-switch>
          </a-space>
        </span>
        <span slot="Method" slot-scope="Method">
            <a-tag :color="Method|apiTagColor">{{ Method|apiTagLabel }}</a-tag>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
const ApiColumns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.ID - b.ID,
    scopedSlots: {customRender: 'ID'},
  },
  {
    title: '路径',
    dataIndex: 'Path',
    align: 'left',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Path - b.Path,
    scopedSlots: {customRender: 'Path'},
  },
  {
    title: '方法',
    dataIndex: 'Method',
    align: 'left',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Method - b.Method,
    scopedSlots: {customRender: 'Method'},
  },
  {
    title: '授权',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'apiDoSome'},
  },
];
const apiMethodOptions = [
  {
    value: 'POST',
    label: '创建',
    color: 'green'
  },
  {
    value: 'GET',
    label: '查看',
    color: 'blue'
  },
  {
    value: 'PUT',
    label: '更新',
    color: 'cyan'
  },
  {
    value: 'DELETE',
    label: '删除',
    color: 'red'
  }
];

export default {
  name: "RoleApi",
  data() {
    return {
      ApiData: [],
      ApiColumns,
      ApiSearchDate: {},
      ApiSearchShow: false,
      ApiPaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 100,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.ApiPaginationOption.defaultCurrent = current
          this.ApiPaginationOption.defaultPageSize = pageSize
          this.apiGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.ApiPaginationOption.defaultCurrent = current
          this.ApiPaginationOption.defaultPageSize = pageSize
          this.apiGets()
        },
      },
      RoleApiPutLoading: false,
      RoleItem: {},
      RoleApiIds: [],
    };
  },
  computed: {
    apiHasSelected() {
      return (n) => {
        return this.RoleApiIds.indexOf(n) !== -1
      }
    }
  },
  created() {
    this.getRoleItem()
    this.apiGets()
  },
  methods: {
    apiSearch() {
      this.ApiSearchShow = !this.ApiSearchShow
      this.ApiPaginationOption.defaultCurrent = 1
    },
    apiEmptySearch() {
      this.ApiSearchDate = {}
      this.ApiPaginationOption.defaultCurrent = 1
    },
    async apiGets() {
      const {data: res} = await this.$http.post('apis', this.ApiSearchDate, {
        params: {
          pageNum: this.ApiPaginationOption.defaultCurrent,
          pageSize: this.ApiPaginationOption.defaultPageSize,
        }
      })
      this.ApiPaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.ApiData = res.data.apis
      this.ApiPaginationOption.total = res.data.total
      this.ApiSearchShow = false
    },
    roleApiSwitchChange(record) {
      if (this.apiHasSelected(record.ID)) {
        this.RoleApiIds.splice(this.RoleApiIds.indexOf(record.ID), 1)
        this.RoleItem.Api.splice(this.RoleItem.Api.findIndex(item => {
          if (item.ID === record.ID) {
            return true
          }
        }), 1)
      } else {
        this.RoleApiIds.push(record.ID)
        this.RoleItem.Api.push(record)
      }
      console.log(this.RoleApiIds)
      // console.log(this.RoleItem)
    },
    async getRoleItem() {
      console.log(this.RoleApiIds)
      if (this.$route.params.item === undefined) {
        return this.$message.warn('角色不能为空，重新点角色修改权限')
      }
      this.RoleItem = this.$route.params.item
      const {data: res} = await this.$http.get('role/' + this.RoleItem.ID)
      this.RoleItem = res.data.role
      res.data.role.Api.forEach((item) => {
        this.RoleApiIds.push(item.ID)
      })
      console.log(this.RoleApiIds)
    },
    async roleApiPut() {
      this.RoleApiPutLoading = true
      const {data: res} = await this.$http.put('role/' + this.RoleItem.ID, this.RoleItem)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'RoleAdmin'}).catch((err) => err)
      this.RoleApiPutLoading = false
    },
  },
  filters: {
    filterTimeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    },
    apiTagColor(val) {
      const target = apiMethodOptions.filter(item => item.value === val)[0]
      return target.color
    },
    apiTagLabel(val) {
      const target = apiMethodOptions.filter(item => item.value === val)[0]
      return `${target.value} | ${target.label}`
    },
  }
};
</script>

<style>

.apiSearchRow > .ant-col, .apiSearchBtn {
  margin-top: 10px;
}

.ant-table-column-title {
  font-weight: bold;
}

#input-switch {
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa
}

</style>
