import Vue from 'vue'
import VueRouter from 'vue-router'
import Login from "../views/Login"
import Admin from "../views/Admin"
import Index from "../components/admin/Index";

import EnumAdmin from "../components/enum/EnumAdmin";
import EnumDetail from "../components/enum/EnumDetail";

import NotifyDetail from "../components/notify/NotifyDetail";
import NotifyAdmin from "../components/notify/NotifyAdmin";

import ApiAdmin from "../components/user/ApiAdmin";
import ApiDetail from "../components/user/ApiDetail";

import RoleAdmin from "../components/user/RoleAdmin";
import RoleApi from "../components/user/RoleApi";
import RoleDetail from "../components/user/RoleDetail";

import UserAdmin from "../components/user/UserAdmin";
import UserDetail from "../components/user/UserDetail";


import MenuAdmin from "../components/user/MenuAdmin";
import MenuDetail from "../components/user/MenuDetail";
import MenuTree from "../components/user/MenuTree";

import DepartmentAdmin from "../components/user/DepartmentAdmin";
import DepartmentDetail from "../components/user/DepartmentDetail";
import DepartmentTree from "../components/user/DepartmentTree";

import StockListen from "../views/StockListen";
import StockAdmin from "../components/stock/StockAdmin";
import StockDetail from "../components/stock/StockDetail";

import ConnectInfoAdmin from "../components/db_manage/ConnectInfoAdmin";
import ConnectInfoDetail from "../components/db_manage/ConnectInfoDetail";

import ServerStatus from "../components/server_status/ServerStatus";
import VersionAdmin from "../components/version/VersionAdmin";
import VersionDetail from "../components/version/VersionDetail";
import SoftwareAdmin from "../components/version/SoftwareAdmin";
import SoftwareDetail from "../components/version/SoftwareDetail";
import DashboardDemo from "../views/DashboardDemo";

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'TempTest',
        component: DashboardDemo,
    },
    {
        path: '/login',
        name: 'Login',
        component: Login,
    },
    {
        path: '/stock_listen',
        name: 'StockListen',
        component: StockListen,
    },
    {
        path: '/admin',
        name: 'Admin',
        component: Admin,
        children: [
            {path: 'index', name: 'Index', component: Index},

            {path: 'user', name: 'UserAdmin', component: UserAdmin},
            {path: 'user_detail', name: 'UserDetail', component: UserDetail},

            {path: 'api', name: 'ApiAdmin', component: ApiAdmin},
            {path: 'api_detail', name: 'ApiDetail', component: ApiDetail},

            {path: 'role', name: 'RoleAdmin', component: RoleAdmin},
            {path: 'role_detail', name: 'RoleDetail', component: RoleDetail},
            {path: 'role_api', name: 'RoleApi', component: RoleApi},

            {path: 'enum', name: 'EnumAdmin', component: EnumAdmin},
            {path: 'enum_detail', name: 'EnumDetail', component: EnumDetail},

            {path: 'notify', name: 'NotifyAdmin', component: NotifyAdmin},
            {path: 'notify_detail', name: 'NotifyDetail', component: NotifyDetail},

            {path: 'menu', name: 'MenuAdmin', component: MenuTree},
            {path: 'menu_detail', name: 'MenuDetail', component: MenuDetail},
            {path: 'menu_tree', name: 'MenuTree', component: MenuTree},

            {path: 'department', name: 'DepartmentAdmin', component: DepartmentTree},
            {path: 'department_detail', name: 'DepartmentDetail', component: DepartmentDetail},
            {path: 'department_tree', name: 'DepartmentTree', component: DepartmentTree},

            {path: 'stock', name: 'StockAdmin', component: StockAdmin},
            {path: 'stock_detail', name: 'StockDetail', component: StockDetail},

            {path: 'connect_info', name: 'ConnectInfoAdmin', component: ConnectInfoAdmin},
            {path: 'connect_info_detail', name: 'ConnectInfoDetail', component: ConnectInfoDetail},

            {path: 'software', name: 'SoftwareAdmin', component: SoftwareAdmin},
            {path: 'software_detail', name: 'SoftwareDetail', component: SoftwareDetail},
            {path: 'version', name: 'VersionAdmin', component: VersionAdmin},
            {path: 'version_detail', name: 'VersionDetail', component: VersionDetail},

            {path: 'server_status', name: 'ServerStatus', component: ServerStatus,},

        ],
    },
]

const router = new VueRouter({
    routes
})

router.beforeEach((to, from, next) => {
    return next()
    // // return next() //直接到指定页面
    // if (to.path === '/tt') return next()
    // let token = window.localStorage.getItem('zys-token')
    // if (!token) token = window.sessionStorage.getItem('zys-token')
    // if (to.path === '/login' || token) return next() //必须，否则会死循环
    // return next('/login') //跳转到登录界面
})

export default router
