<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="softwareSearch">筛选</a-button>
              <a-button type="primary" @click="softwareAdd">新增</a-button>
              <a-button type="primary" :disabled="!softwareHasSelected" @click="softwareDel">删除</a-button>
              <span style="margin-left: 8px">
								<template v-if="softwareHasSelected">
									已选中 {{ SoftwareSelectedRowKeys.length }} 行
								</template>
							</span>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button @click="softwareExportDate">导出</a-button>
              <a-button :disabled="!softwareHasSelected" @click="softwarePrintDate">打印</a-button>
            </a-space>
          </a-col>
        </a-row>
        <div v-if="SoftwareSearchShow">
          <a-row :gutter="[12,8]" class="softwareSearchInput">
            <a-col span="4">
              <a-input v-model="SoftwareSearchDate.ID" addon-before="ID"/>
            </a-col>
            <a-col span="4">
              <a-date-picker type="date" v-model="SoftwareSearchDate.CreatedAt" addon-before="CreatedAt"/>
            </a-col>
            <a-col span="4">
              <a-date-picker type="date" v-model="SoftwareSearchDate.UpdatedAt" addon-before="UpdatedAt"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="SoftwareSearchDate.DeletedAt" addon-before="DeletedAt"/>
            </a-col>
            <a-col span="4">
              <a-input v-model="SoftwareSearchDate.Name" addon-before="名称"/>
            </a-col>
          </a-row>
          <a-row class="softwareSearchBtn">
            <a-space size="large">
              <a-button type="primary" @click="softwareEmptySearch">重置</a-button>
              <a-button type="primary" @click="softwareGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table
          :row-selection="{ selectedRowKeys: SoftwareSelectedRowKeys, onChange: softwareSelectChange }"
          :columns="SoftwareColumns"
          :data-source="SoftwareData"
          rowKey="ID"
          :pagination="SoftwarePaginationOption"
          bordered
          size="small"
      >
        <span slot="CreatedAt" slot-scope="CreatedAt">{{ CreatedAt | timeFormat(CreatedAt) }}</span>
        <span slot="UpdatedAt" slot-scope="UpdatedAt">{{ UpdatedAt | timeFormat(UpdatedAt) }}</span>
        <span slot="softwareDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="softwareRecordUpdate(record)">修改</a-button>
            <a-button type="primary" size="small" @click="versionAdd(record)">发布版本</a-button>
            <a-button type="primary" size="small" @click="versionHistory(record)">历史版本</a-button>
          </a-space>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import xlsx from 'xlsx'
import moment from 'moment'

const SoftwareColumns = [
  {
    title: 'ID',
    dataIndex: 'ID',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.ID - b.ID,
    scopedSlots: {customRender: 'ID'},
  },
  {
    title: 'CreatedAt',
    dataIndex: 'CreatedAt',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.CreatedAt - b.CreatedAt,
    scopedSlots: {customRender: 'CreatedAt'},
  },
  {
    title: 'UpdatedAt',
    dataIndex: 'UpdatedAt',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.UpdatedAt - b.UpdatedAt,
    scopedSlots: {customRender: 'UpdatedAt'},
  },
  {
    title: 'DeletedAt',
    dataIndex: 'DeletedAt',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.DeletedAt - b.DeletedAt,
    scopedSlots: {customRender: 'DeletedAt'},
  },
  {
    title: '名称',
    dataIndex: 'Name',
    align: 'center',
    sortDirections: ['descend', 'ascend'],
    sorter: (a, b) => a.Name - b.Name,
    scopedSlots: {customRender: 'Name'},
  },
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'softwareDoSome'},
  },
];
export default {
  name: "SoftwareAdmin",
  data() {
    return {
      SoftwareData: [],
      SoftwareColumns,
      SoftwareSelectedRowKeys: [],
      SoftwareSearchDate: {},
      SoftwareSearchShow: false,
      SoftwarePaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 10,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.SoftwarePaginationOption.defaultCurrent = current
          this.SoftwarePaginationOption.defaultPageSize = pageSize
          this.softwareGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.SoftwarePaginationOption.defaultCurrent = current
          this.SoftwarePaginationOption.defaultPageSize = pageSize
          this.softwareGets()
        },
      },
    };
  },
  computed: {
    softwareHasSelected() {
      return this.SoftwareSelectedRowKeys.length > 0;
    },
  },
  created() {
    this.softwareGets()
  },
  methods: {
    versionAdd(record) {
      console.log(record, 'version add')
      this.$router.push({
        name: 'VersionDetail',
        params: {formTitle: `新增_${record.Name}_子版本`, softwareId: record.ID}
      }).catch((err) => err)
    },
    versionHistory(record) {
      console.log(record, 'version history')
      this.$router.push({name: 'VersionAdmin', params: {Software: record}}).catch((err) => err)
    },
    softwareAdd() {
      this.$router.push({name: 'SoftwareDetail', params: {formTitle: '新增_软件'}}).catch((err) => err)
    },
    softwareDel() {
      if (this.SoftwareSelectedRowKeys.length > 1) return this.$message.warn('不允许删除多行')
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.softwareDelOk()
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async softwareDelOk() {
      const {data: res} = await this.$http.delete('software/' + this.SoftwareSelectedRowKeys)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.SoftwareSelectedRowKeys = []
      this.softwareGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    softwareRecordUpdate(record) {
      this.$router.push({name: 'SoftwareDetail', params: {formTitle: '修改_软件', formData: record}})
    },
    softwareSearch() {
      this.SoftwareSearchShow = !this.SoftwareSearchShow
      this.SoftwarePaginationOption.defaultCurrent = 1
    },
    softwareEmptySearch() {
      this.SoftwareSearchDate = {}
      this.SoftwarePaginationOption.defaultCurrent = 1
    },
    async softwareGets() {
      const {data: res} = await this.$http.post('software/list', this.SoftwareSearchDate, {
        params: {
          pageNum: this.SoftwarePaginationOption.defaultCurrent,
          pageSize: this.SoftwarePaginationOption.defaultPageSize,
        }
      })
      this.SoftwarePaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.SoftwareData = res.data.softwareList
      this.SoftwarePaginationOption.total = res.data.total
      this.SoftwareSearchShow = false
    },
    softwareSelectChange(selectedRowKeys) {
      console.log('SoftwareSelectedRowKeys changed: ', selectedRowKeys);
      this.SoftwareSelectedRowKeys = selectedRowKeys;
    },
    async softwareExportDate() {
      const {data: res} = await this.$http.post('software/list/exp', this.SoftwareSearchDate)
      if (res.code !== 200) return this.$message.warn(res.msg)
      let tableData = res.data.softwareList
      let ws = xlsx.utils.aoa_to_sheet(tableData)
      let wb = xlsx.utils.book_new()
      xlsx.utils.book_append_sheet(wb, ws, 'software') // 工作簿名称
      xlsx.writeFile(wb, 'software.xlsx') // 保存的文件名
    },
    softwarePrintDate() {
      console.log('print data')
      this.SoftwareSelectedRowKeys.forEach((item, index) => {
        console.log(item, index)
      })
      this.SoftwareSelectedRowKeys = []
    },
  },
  filters: {
    timeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    }
  }
};
</script>

<style>

.softwareSearchBtn {
  text-align: center;
}

.softwareSearchInput, .softwareSearchBtn {
  margin-top: 10px !important;
}

.ant-table-column-title {
  font-weight: bold;
}

.ant-input-disabled, .ant-input-number-disabled {
  color: #000000A6 !important;
  background-color: #fafafa !important;
}

</style>
