<template>
  <div>
    <a-card>
      <div>
        <a-row type="flex" justify="space-between">
          <a-col span="18">
            <a-space size="small">
              <a-button type="primary" @click="departmentSearch">筛选</a-button>
              <a-button type="primary" @click="departmentAdd">新增</a-button>
              <a-button type="primary" :disabled="!departmentHasSelected" @click="departmentDel">删除</a-button>
              <span style="margin-left: 8px">
								<template v-if="departmentHasSelected">
									已选中 {{ DepartmentSelectedRowKeys.length }} 行
								</template>
							</span>
            </a-space>
          </a-col>
          <a-col span="6" style="display: flex; justify-content: flex-end; align-items: center;">
            <a-space size="small">
              <a-button @click="departmentExportDate">导出</a-button>
              <a-button :disabled="!departmentHasSelected" @click="departmentPrintDate">打印</a-button>
            </a-space>
          </a-col>
        </a-row>
        <div v-if="DepartmentSearchShow">
          <a-row :gutter="6" class="departmentSearchRow">
					<a-col span="4">
						<a-input v-model.number="DepartmentSearchDate.PID" addon-before="PID" />
					</a-col>
					<a-col span="4">
						<a-input v-model.number="DepartmentSearchDate.Level" addon-before="层级" />
					</a-col>
					<a-col span="4">
						<a-input v-model="DepartmentSearchDate.Name"  addon-before="名称" />
					</a-col>
					<a-col span="4">
						<a-input v-model.number="DepartmentSearchDate.Sort" addon-before="排序" />
					</a-col>
					<a-col span="4">
						<a-input id="input-switch" disabled defaultValue="启用">
							<a-switch v-model="DepartmentSearchDate.IsEnable" slot="addonAfter"/>
						</a-input>
					</a-col>
          </a-row>
          <a-row class="departmentSearchBtn" style="text-align: center;">
            <a-space size="large">
              <a-button type="primary" @click="departmentEmptySearch">重置</a-button>
              <a-button type="primary" @click="departmentGets">筛选</a-button>
            </a-space>
          </a-row>
        </div>
      </div>
      <a-table
          :row-selection="{ selectedRowKeys: DepartmentSelectedRowKeys, onChange: departmentSelectChange }"
          :columns="DepartmentColumns"
          :data-source="DepartmentData"
          rowKey="ID"
          :pagination="DepartmentPaginationOption"
          bordered
          size="small"
      >
                <span slot="IsEnable" slot-scope="IsEnable">
                    <a-icon v-if="IsEnable" type="check" style="color: green" />
                    <a-icon v-if="!IsEnable" type="close" style="color: red" />
                </span>
        <span slot="departmentDoSome" slot-scope="record">
          <a-space size="small">
            <a-button type="primary" size="small" @click="departmentRecordUpdate(record)">修改</a-button>
          </a-space>
        </span>
      </a-table>
    </a-card>
  </div>
</template>
<script>
import xlsx from 'xlsx'
import moment from 'moment'

const DepartmentColumns = [
	{
		title: 'ID',
		dataIndex: 'ID',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.ID - b.ID,
		scopedSlots: {customRender: 'ID'},
	},
	{
		title: 'PID',
		dataIndex: 'PID',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.PID - b.PID,
		scopedSlots: {customRender: 'PID'},
	},
	{
		title: '层级',
		dataIndex: 'Level',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.Level - b.Level,
		scopedSlots: {customRender: 'Level'},
	},
	{
		title: '名称',
		dataIndex: 'Name',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.Name - b.Name,
		scopedSlots: {customRender: 'Name'},
	},
	{
		title: '排序',
		dataIndex: 'Sort',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.Sort - b.Sort,
		scopedSlots: {customRender: 'Sort'},
	},
	{
		title: '启用',
		dataIndex: 'IsEnable',
		align: 'center',
		sortDirections: ['descend', 'ascend'],
		sorter: (a, b) => a.IsEnable - b.IsEnable,
		scopedSlots: {customRender: 'IsEnable'},
	},
  {
    title: '操作',
    fixed: 'right',
    align: 'center',
    width: 100,
    scopedSlots: {customRender: 'departmentDoSome'},
  },
];
export default {
  name: "DepartmentAdmin",
  data() {
    return {
      DepartmentData: [],
      DepartmentColumns,
      DepartmentSelectedRowKeys: [],
      DepartmentSearchDate: {},
      DepartmentSearchShow: false,
      DepartmentPaginationOption: {
        defaultCurrent: 1,
        defaultPageSize: 10,
        pageSizeOptions: ['10', '20', '100'],
        total: 0,
        showSizeChanger: true,
        showTotal: (total) => '共' + total + '条',
        onChange: (current, pageSize) => {
          this.DepartmentPaginationOption.defaultCurrent = current
          this.DepartmentPaginationOption.defaultPageSize = pageSize
          this.departmentGets()
        },
        onShowSizeChange: (current, pageSize) => {
          this.DepartmentPaginationOption.defaultCurrent = current
          this.DepartmentPaginationOption.defaultPageSize = pageSize
          this.departmentGets()
        },
      },
    };
  },
  computed: {
    departmentHasSelected() {
      return this.DepartmentSelectedRowKeys.length > 0;
    },
  },
  created() {
    this.departmentGets()
  },
  methods: {
    departmentAdd() {
      this.$router.push({name: 'DepartmentDetail', params: {formTitle: 'Add Department'}}).catch((err) => err)
    },
    departmentDel() {
      if (this.DepartmentSelectedRowKeys.length > 1) return this.$message.warn('不允许删除多行')
      this.$confirm({
        title: '删除确认',
        content: '不可恢复，继续删除？',
        onOk: () => {
          this.departmentDelOk()
        },
        onCancel: () => {
          this.$message.info('取消')
        }
      })
    },
    async departmentDelOk() {
      const {data: res} = await this.$http.delete('department/' + this.DepartmentSelectedRowKeys)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.DepartmentSelectedRowKeys = []
      this.departmentGets().catch((err) => err)
      this.$message.success('删除成功')
    },
    departmentRecordUpdate(record) {
      this.$router.push({name: 'DepartmentDetail', params: {formTitle: 'Update Department', formData: record}})
    },
    departmentSearch() {
      this.DepartmentSearchShow = !this.DepartmentSearchShow
      this.DepartmentPaginationOption.defaultCurrent = 1
    },
    departmentEmptySearch() {
      this.DepartmentSearchDate = {}
      this.DepartmentPaginationOption.defaultCurrent = 1
    },
    async departmentGets() {
      const {data: res} = await this.$http.post('departments', this.DepartmentSearchDate, {
        params: {
          pageNum: this.DepartmentPaginationOption.defaultCurrent,
          pageSize: this.DepartmentPaginationOption.defaultPageSize,
        }
      })
      this.DepartmentPaginationOption.total = 0
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.DepartmentData = res.data.departments
      this.DepartmentPaginationOption.total = res.data.total
      this.DepartmentSearchShow = false
    },
    departmentSelectChange(selectedRowKeys) {
      console.log('DepartmentSelectedRowKeys changed: ', selectedRowKeys);
      this.DepartmentSelectedRowKeys = selectedRowKeys;
    },
    async departmentExportDate() {
      const {data: res} = await this.$http.post('departments/exp', this.DepartmentSearchDate)
      if (res.code !== 200) return this.$message.warn(res.msg)
      let tableData = res.data.departments
      let ws = xlsx.utils.aoa_to_sheet(tableData)
      let wb = xlsx.utils.book_new()
      xlsx.utils.book_append_sheet(wb, ws, 'department') // 工作簿名称
      xlsx.writeFile(wb, 'department.xlsx') // 保存的文件名
    },
    departmentPrintDate() {
      console.log('print data')
      this.DepartmentSelectedRowKeys.forEach((item, index) => {
        console.log(item, index)
      })
      this.DepartmentSelectedRowKeys = []
    },
  },
  filters: {
    filterTimeFormat(val) {
      return val ? moment(val).format("YYYY-MM-DD") : '';
    }
  }
};
</script>

<style>

.departmentSearchRow > .ant-col, .departmentSearchBtn {
  margin-top: 10px;
}

.ant-table-column-title {
  font-weight: bold;
}

#input-switch{
  color: rgba(0, 0, 0, 0.65);
  background-color: #fafafa
}

</style>
