<template>
  <div>
    <a-card :title="DepartmentFormTitle">
      <a-form-model ref="DepartmentFormRef" :rules="DepartmentFormRules" :model="DepartmentFormData">
        <a-form-model-item hasFeedback prop="PID" label="PID">
          <a-input-number :disabled="DepartmentItemDisabled" v-model.number="DepartmentFormData.PID"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Level" label="层级">
          <a-input-number :disabled="DepartmentItemDisabled" v-model.number="DepartmentFormData.Level"/>
        </a-form-model-item>
<!--        <a-form-model-item hasFeedback prop="Code" label="编号">-->
<!--          <a-input v-model="DepartmentFormData.Code"/>-->
<!--        </a-form-model-item>-->
        <a-form-model-item hasFeedback prop="Name" label="名称">
          <a-input v-model="DepartmentFormData.Name"/>
        </a-form-model-item>
        <a-form-model-item class="departmentFormBtn">
          <a-space>
            <a-button type="info" @click="departmentFormReset">取消</a-button>
            <a-button type="primary" @click="departmentFormOk">确定</a-button>
          </a-space>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
export default {
  name: "DepartmentDetail",
  data() {
    return {
      DepartmentFormTitle: '',
      DepartmentFormData: {},
      DepartmentFormRules: {
        Name: [
          {required: true, message: '名称不能为空', trigger: 'blur'},
        ],
      },
      DepartmentItemDisabled: false,
    }
  },
  created() {
    this.DepartmentFormTitle = this.$route.params.formTitle
    if (this.DepartmentFormTitle === 'Add Department') {
      this.DepartmentFormData.PID = this.$route.params.formData.ID
      this.DepartmentFormData.Level = this.$route.params.formData.Level + 1
      this.DepartmentFormData.Code = this.$route.params.formData.Code
      this.DepartmentItemDisabled = true
    } else if (this.DepartmentFormTitle === 'Add L1 Department') {
      this.DepartmentFormData.PID = 0
      this.DepartmentFormData.Level = 1
      this.DepartmentFormData.Code = '1'
      this.DepartmentItemDisabled = true
    } else if (this.DepartmentFormTitle === 'Update Department') {
      this.DepartmentFormData = this.$route.params.formData
      this.DepartmentItemDisabled = true
    } else {
      this.$router.push({name: 'DepartmentAdmin'}).catch((err) => err)
    }
  },
  methods: {
    departmentFormReset() {
      this.$refs.DepartmentFormRef.resetFields();
      this.$router.push({name: 'DepartmentAdmin'}).catch((err) => err)
    },
    departmentFormOk() {
      if (this.DepartmentFormTitle === 'Add Department') {
        this.departmentAdd()
      } else if (this.DepartmentFormTitle === 'Add L1 Department') {
        this.departmentAdd()
      } else if (this.DepartmentFormTitle === 'Update Department') {
        this.departmentUpdate()
      } else {
        this.$message.error('提交错误')
        this.$router.push({name: 'DepartmentAdmin'}).catch((err) => err)
      }
    },
    async departmentAdd() {
      const {data: res} = await this.$http.post('department', this.DepartmentFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('新增成功')
      this.$router.push({name: 'DepartmentAdmin'}).catch((err) => err)
    },
    async departmentUpdate() {
      const {data: res} = await this.$http.put('department/' + this.DepartmentFormData.ID, this.DepartmentFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'DepartmentAdmin'}).catch((err) => err)
    },
  },
}
</script>

<style scoped>

.departmentFormBtn {
  display: flex;
  justify-content: flex-end;
}

</style>
