<template>
  <div>
    <a-card :title="SoftwareFormTitle">
      <a-form-model ref="SoftwareFormRef" :rules="SoftwareFormRules" :model="SoftwareFormData">
        <!--				<a-form-model-item hasFeedback prop="ID" label="ID">-->
        <!--					<a-input :disabled="SoftwareItemDisabled" v-model="SoftwareFormData.ID" />-->
        <!--				</a-form-model-item>-->
        <!--				<a-form-model-item hasFeedback prop="CreatedAt" label="CreatedAt">-->
        <!--					<a-date-picker :disabled="SoftwareItemDisabled" v-model="SoftwareFormData.CreatedAt" />-->
        <!--				</a-form-model-item>-->
        <!--				<a-form-model-item hasFeedback prop="UpdatedAt" label="UpdatedAt">-->
        <!--					<a-date-picker :disabled="SoftwareItemDisabled" v-model="SoftwareFormData.UpdatedAt" />-->
        <!--				</a-form-model-item>-->
        <!--				<a-form-model-item hasFeedback prop="DeletedAt" label="DeletedAt">-->
        <!--					<a-input :disabled="SoftwareItemDisabled" v-model="SoftwareFormData.DeletedAt" />-->
        <!--				</a-form-model-item>-->
        <a-form-model-item hasFeedback prop="Name" label="名称">
          <a-input v-model="SoftwareFormData.Name"/>
        </a-form-model-item>
        <a-form-model-item class="softwareFormBtn">
          <a-space>
            <a-button type="info" @click="softwareFormReset">取消</a-button>
            <a-button type="primary" @click="softwareFormOk">确定</a-button>
          </a-space>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
export default {
  name: "SoftwareDetail",
  data() {
    return {
      SoftwareFormTitle: '',
      SoftwareFormData: {},
      SoftwareFormRules: {
        ID: [
          {required: true, message: 'ID不能为空', trigger: 'blur'},
        ],
        CreatedAt: [
          {required: true, message: 'CreatedAt不能为空', trigger: 'blur'},
        ],
        UpdatedAt: [
          {required: true, message: 'UpdatedAt不能为空', trigger: 'blur'},
        ],
        DeletedAt: [
          {required: true, message: 'DeletedAt不能为空', trigger: 'blur'},
        ],
        Name: [
          {required: true, message: '名称不能为空', trigger: 'blur'},
        ],
      },
      SoftwareItemDisabled: false,
    }
  },
  created() {
    this.SoftwareFormTitle = this.$route.params.formTitle
    if (this.SoftwareFormTitle === '新增_软件') {
      console.log('add')
    } else if (this.SoftwareFormTitle === '修改_软件') {
      this.SoftwareFormData = this.$route.params.formData
      this.SoftwareItemDisabled = true
    } else {
      this.$router.push({name: 'SoftwareAdmin'}).catch((err) => err)
    }
  },
  methods: {
    softwareFormReset() {
      this.$refs.SoftwareFormRef.resetFields();
      this.$router.push({name: 'SoftwareAdmin'}).catch((err) => err)
    },
    softwareFormOk() {
      if (this.SoftwareFormTitle === '新增_软件') {
        this.softwareAdd()
      } else if (this.SoftwareFormTitle === '修改_软件') {
        this.softwareUpdate()
      } else {
        this.$message.error('提交错误')
        this.$router.push({name: 'SoftwareAdmin'}).catch((err) => err)
      }
    },
    async softwareAdd() {
      const {data: res} = await this.$http.post('software', this.SoftwareFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('新增成功')
      this.$router.push({name: 'SoftwareAdmin'}).catch((err) => err)
    },
    async softwareUpdate() {
      const {data: res} = await this.$http.put('software/' + this.SoftwareFormData.ID, this.SoftwareFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'SoftwareAdmin'}).catch((err) => err)
    },
  },
}
</script>

<style scoped>

.softwareFormBtn {
  display: flex;
  justify-content: flex-end;
}

.ant-input-disabled, .ant-input-number-disabled {
  color: #000000A6 !important;
  background-color: #fafafa !important;
}

</style>
