import Vue from "vue";
import axios from "axios";

let Url="http://localhost/api/v1" //异步地址，host改成本机，端口与vue.config.js的开发端口一致，部署时修改为默认80
// let Url="http://localhost:8880/api/v1" //异步地址，host改成本机，端口与vue.config.js的开发端口一致，部署时修改为默认80

axios.defaults.baseURL = Url
axios.interceptors.request.use(config => {
    let token = window.localStorage.getItem('zys-token')
    if (!token) token = window.sessionStorage.getItem('zys-token')
    config.headers.Authorization = `Bearer ${token}`
    return config
})
Vue.prototype.$http = axios

export {Url} //导出供页面引用