<template>
  <div>
    <a-card :title="NotifyFormTitle">
      <a-form-model ref="NotifyFormRef" :rules="NotifyFormRules" :model="NotifyFormData">
        <a-form-model-item hasFeedback prop="Title" label="标题">
          <a-input :disabled="NotifyItemDisabled" v-model="NotifyFormData.Title"/>
        </a-form-model-item>
        <a-form-model-item hasFeedback prop="Content" label="内容">
          <a-input :disabled="NotifyItemDisabled" v-model="NotifyFormData.Content"/>
        </a-form-model-item>
        <a-form-model-item class="notifyFormBtn">
          <a-space>
            <a-button type="info" @click="notifyFormReset">取消</a-button>
            <a-button type="primary" @click="notifyFormOk">确定</a-button>
          </a-space>
        </a-form-model-item>
      </a-form-model>
    </a-card>
  </div>
</template>

<script>
export default {
  name: "NotifyDetail",
  data() {
    return {
      NotifyFormTitle: '',
      NotifyFormData: {},
      NotifyFormRules: {
        Title: [
          {required: true, message: '标题不能为空', trigger: 'blur'},
        ],
        Content: [
          {required: true, message: '内容不能为空', trigger: 'blur'},
        ],
      },
      NotifyItemDisabled: false,
    }
  },
  created() {
    this.NotifyFormTitle = this.$route.params.formTitle
    if (this.NotifyFormTitle === 'Add Notify') {
      console.log('add')
    } else if (this.NotifyFormTitle === 'Update Notify') {
      this.NotifyFormData = this.$route.params.formData
      this.NotifyItemDisabled = true
    } else {
      this.$router.push({name: 'NotifyAdmin'}).catch((err) => err)
    }
  },
  methods: {
    notifyFormReset() {
      this.$refs.NotifyFormRef.resetFields();
      this.$router.push({name: 'NotifyAdmin'}).catch((err) => err)
    },
    notifyFormOk() {
      if (this.NotifyFormTitle === 'Add Notify') {
        this.notifyAdd()
      } else if (this.NotifyFormTitle === 'Update Notify') {
        this.notifyUpdate()
      } else {
        this.$message.error('提交错误')
        this.$router.push({name: 'NotifyAdmin'}).catch((err) => err)
      }
    },
    async notifyAdd() {
      const {data: res} = await this.$http.post('notify', this.NotifyFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('新增成功')
      this.$router.push({name: 'NotifyAdmin'}).catch((err) => err)
    },
    async notifyUpdate() {
      const {data: res} = await this.$http.put('notify/' + this.NotifyFormData.ID, this.NotifyFormData)
      if (res.code !== 200) return this.$message.warn(res.msg)
      this.$message.success('修改成功')
      this.$router.push({name: 'NotifyAdmin'}).catch((err) => err)
    },
  },
}
</script>

<style scoped>

.notifyFormBtn {
  display: flex;
  justify-content: flex-end;
}

</style>
